.search.search-results{
    .content{
        width: 100%;
        padding: 0 15px;
    }
}
.search--recipes{
    margin-top: 140px;
    h1{
        text-align: center;
        color: #a0a0a0;
        span{
            color: $link-color;
        }
    }
    .search-page-form{
        position: relative;
        max-width: 600px;
        margin: 0 auto 30px auto;
        form {
            box-shadow: 0px 6px 16px 0px rgba(127, 127, 127, 0.1);
            input[type="search"] {
                min-height: 62px;
                border: 3px solid #eeede8;
                &::placeholder {
                    color: #a0a0a0;
                    font-size: 17.29px;
                    font-weight: 700;
                    text-transform: uppercase;
                }
            } 
        }
        input[type="submit"], .fa-search {
            position: absolute;
            right: 3%;
            top: 50%;
            transform: translate(0, -50%);
            background: transparent;
        }
        input[type="submit"]{
            z-index: 50;
            padding: 10px 18px;
            &:hover &+i{
                color: $link-color;
            }
            color: transparent;
        }
        input[type="submit"]:hover &>i{
            color: $link-color;
        }
        .fa-search{
            font-size: 35px;
            color: $black-color;
            z-index: 40;
            padding-left: 15px;
            border-left: 1px solid #eeede8;
            transition: all 0.6s ease;
        }
    }
    .post-wrap{
        img{
            width: 100%;
            height: 200px;
            object-fit: cover;
        }
        a{
            font-weight: 700;
        }
        .search-box{
            border-left: 1px solid $gray4-color;
            border-right: 1px solid $gray4-color;
            border-bottom: 1px solid $gray4-color;
            text-align: center;
            padding: 30px 20px 30px 20px;
            a {
                color: #919191;
                text-transform: capitalize;
                font-size: 17px;
                font-weight: 700;
                margin-top: 0.6rem;

                &:hover,
                &:focus {
                    color: $link-color;
                    outline: none;
                }
            }
            .search-post-excerpt{
                text-align: left;
                margin-top: 20px;
                display: block;
                color: #a0a0a0;
            }
        }
    }
    @media screen and( max-width: $medium-phone){
       .post-wrap{
           span{
               display: block;
               text-align: center;
           }
           a{
                display: block;
                margin-top: 10px;
           }
       } 
    }
}