.btn {
  border-radius: 5px;
  font-size: 15.34px;
  font-weight: 700;
  text-transform: uppercase;
  @include inline-flexcen(center, center);

  &.btn--transparent {
    border: 2px solid $white-color;
    color: $white-color;
    @include min-w-h(156, 51);
    position: relative;
    &:hover {
      // background-color: $white-color;
      color: $link-color;
      transition: all 0.8s ease;
    }
    @include effect-solid(155px, 51px, $white-color);
    
  }
  &.btn--hero{
    transition: all 0.6s ease;
    &:hover {
      box-shadow: inset 155px 0 0 0 $white-color;
      color: $link-color;
      // border: 2px solid $link-color;
    }
  }

  &.btn-transparent-lg {
    // width: 100%;
    min-height: 50px;
    border: 2px solid $white-color;
    font-size: 20px;
    font-weight: 700;
    color: $white-color;
    max-width: 800px;
    margin: 0 auto;

    &:hover {
      border: 2px solid transparent;
      box-shadow: 0 0 8px 3px $link-color;
      border: 0;
    }
  }

  &.btn-transparent-hover{
    // @include min-w-h(100%, 50);
    position: relative;
    //justify-content: center;
    @include flexcen(center, center);
    &:hover {
      border: none;
      box-shadow: none;
      border: 0;
    }
    @include effect-solid(100%, 50px, $link-color);
  }

  &.btn--red-small {
    @include min-w-h(83, 24);
    background-color: $link-color;
    color: $white-color;
    font-size: 13px;
    border: 1px solid $link-color;
    transition: all 0.6s ease;

    &:hover {
     // background-color: $white-color;
      color: $link-color;
    }
  }

  &.btn--red-hover{
      color: $white-color;
      background: $link-color;
      font-weight: 600;
      border: 2px solid $link-color;
      @include min-w-h(83, 24);
      box-shadow: inset 0 0 0 0 $white-color;
      transition: all ease 0.8s;
    &:hover {
        box-shadow: inset 85px 0 0 0 $white-color;
        color: $link-color;
        border: 2px solid $link-color;
    }
        
    // @media (max-width: $medium-phone){
    //   pointer-events: none;
    // }
  }
  &.btn--red {
    @include min-w-h(105, 46);
    background-color: $link-color;
    color: $white-color;
    font-size: 13.09px;
    font-weight: 900;
    transition: all 0.8s ease;
    &:hover {
      background-color: transparent;
      color: $link-color;
      border: 2px solid $link-color;
    }
  }

  &.btn--red-hover1{
    color: $white-color;
    background: $link-color;
    @include min-w-h(105, 46);
    border: 2px solid $link-color;
    &:hover {
      // box-shadow: inset 102px 0 0 0 $white-color;
      // color: $link-color;
      background: $link-color;
      color: $white-color;
      border: 2px solid $link-color;
    }
  }

  &.btn--transparent-red {
    border: 2px solid $link-color;
    color: $link-color;
    background-color: transparent;
    font-weight: 800;
    transition: all 0.8s ease;
    @include min-w-h(190.5, 51);
    position: relative;
    &:hover {
      // background-color: $link-color;
      color: $white-color;
    }
    @include effect-solid(190.5px, 51px, $link-color);
  }

  &.btn--transparent-gray {
    border: 2px solid #c4c4c4;
    border-radius: 3px;
    color: #c4c4c4;
    transition: all 0.8s ease;
    @include min-w-h(83, 28);

    &:hover {
      background-color: $link-color;
      color: $white-color;
      border: 2px solid $link-color;
    }
  }

  &.btn--gray-hover{
    &:hover {
      background-color: transparent;
      box-shadow: inset 83px 0 0 0 $link-color;
      color: $white-color;
      border: 2px solid $link-color;
    }
  }

  &.btn--gray {
    @include min-w-h(146, 41);
    background-color: $gray3-color;
    font-size: 13.58px;
    color: $white-color;
    position: relative;

    &:hover {
      background-color: $link-color;
    }
    // @include effect-solid(146px, 41px, $link-color);
  }

  &.btn--white {
    @include min-w-h(88, 29);
    background-color: $white-color;
    color: $link-color;
    font-size: 13px;
    border: 1px solid $white-color;
    transition: all 0.6s ease;

    &:hover {
      background-color: $link-color;
      color: $white-color;
    }
  }
  &.btn--event{
    padding: 8px 25px;
    font-size: 16px;
    font-weight: 700;
    z-index: 10 ;
    position: absolute;
    left: 50%;
    bottom: -20px;
    transform: translateX(-50%);
    min-width: 192px;
    &:hover{
      color: $white;
    }
  }
  &.btn--event2{
    padding: 5px 20px;
    font-size: 15px;
    font-weight: 700;
    &:hover{
      color: $white;
    }
  }
}
.button--play{
  position: absolute;
  top: 50%!important;  
  left: 50%!important; 
  transform: translate(-30px,-30px)!important; 
  img{
    width: 60px!important;
    height: 60px !important;
    background: transparent!important;
  }
  
}