// Featured
.section--featured-resp{
    padding-top: 0;
    @media screen and(max-width: $tablet){
        margin-top: 0px;
    }
}
.section--featured__image-correct{
    margin-top: 42px;
    margin-bottom: 70px;
}
.section--featured {
    background-color: $link-color;
    padding-top: 0;

    &__image {
        position: relative;
        margin-left: 2rem;
        // width: fit-content;
        width: 100%;
        max-width: 500px;
        @media screen and(max-width: $small-tablet){
            width: auto;
        }

        a {
            position: absolute;
            bottom: 5%;
            left: -2%;
        }
        img{
            width: 100%;
            max-height: 310px;
            object-fit: cover;
        }
    }

    &__content {
        min-height: 450px;
        @include flexcen(center, flex-start);
        flex-direction: column;
        color: $white-color;
        margin-right: 2rem;
        padding-left: 40px;

        h4 {
            font-size: 17px;
            margin-bottom: 0.5rem;
        }

        h5 {
            font-size: 35px;
            text-transform: lowercase;
            margin-bottom: 0.5rem;
        }

        p {
            font-size: 17px;
            line-height: 1.529;
            font-weight: 500;
            max-width: 645px;
            margin-bottom: 0;
        }
    }
    @media screen and(max-width: $tablet){
        // margin-top: 32px;
        // padding-top: 20px;
        padding-top: 36px;
        .logged-in &{
            margin-top: 32px;
        }
        &__image{
            margin: 40px auto 0 auto;
        }
        &__content{
            align-items: center;
            min-height: 260px;
            padding: 15px 0;
            margin-right: 0;
            p{
                text-align: justify;
            }
            h5{
                text-align: center;
            }
        }
    }
}

// Search Filter
.section--filter {
    &__content {
        min-height: 92px;
        border-bottom: 3px solid $black-color;
        margin: 0 2rem;
    }
    .searchandfilter {
        transform: translateY(10px);
        li[data-sf-field-input-type=checkbox] label ,
        h4 {
            font-size: 18px;
            color: #be0712;
            text-transform: uppercase;
            font-weight: 700;
            margin-right: 25px;
            display: inline-block;
            align-items: center;
            position: relative;
            top: 0px;
        }
        ul{
            display: flex;
            justify-content: space-between;
            align-items: center;
            &>li>ul:not(.children) {
                display: inline-flex;
                
            }
            li li {
                input {
                    display: none;
                    &:checked + label{
                        &::after {
                            background-image: url(../../images/post/checked.svg);
                            background-repeat: no-repeat;
                            background-size: 60%;
                            background-position: center;
                        }
                    }
                }
                label {
                    position: relative;
                    padding-left: 40px;
                    cursor: pointer;
                    &::before {
                        content: "";
                        position: relative;
                        top: 7px;
                        margin-right: 7px;
                    }
                    &::after {
                        content: "";
                        position: absolute;
                        // top: 50%;
                        top: 9px;
                        left: 0;
                        // transform: translateY(-50%);
                        width: 27px;
                        height: 24px;
                        border-radius: 4px;
                        border: 1px solid gray;
                        background-color: transparent;
                    }
                }
                &:nth-child(1) {
                    label::before {
                        content: url(../../images/post/profile.png);
                    }
                }
                &:nth-child(2) {
                    label::before {
                        content: url(../../images/post/whisk.png);
                    }
                }
                &:nth-child(3) {
                    label::before {
                        content: url(../../images/post/podcast.png);
                    }
                }
            }
            .sf-field-search{
                position: relative;
                flex: 1;
                ::before{
                    content:'';
                    @include abs-position(22px, 10px, auto, auto);
                    width: 28px;
                    height: 28px;
                    background-image: url('../../images/post/search.png');
                    background-size: cover;
                }
                label{
                    width: 100%;
                    input{
                        border: none;
                        background-color: #e3e3e3;
                    }
                    input::placeholder{
                        color: #b6b6b6;
                    }
                    @media screen and (max-width: $tablet){
                        input{
                            border: 1px solid #ddd;
                        }
                        &::before{
                            top: 22px;
                            right: 10px;
                        }
                    }
                }
                
            }
            @media (max-width: $tablet){
                display: revert;
            }
            @media (max-width: $medium-phone){
                &>li>ul:not(.children) {
                    display: block;
                    
                }
            }
        }
        .sf-field-post_type:not(:first-of-type) {
            display: none;
            margin-top: 25px;
            select{
                outline: none;
            }
            @media screen and(max-width: $medium-phone){
                display: flex;
                align-items: center;
                justify-content: center;
                h4{
                    display: none;
                }
                label{
                    width: 100%;
                }
            }
        }
        @media screen and(max-width: $medium-phone){
            .sf-field-post_type:first-child{
                display: none;
            }
        }
        
    }
    @media screen and (max-width: $tablet){
        &__content{
            border-bottom: none;
        }
    }
}

// Blog
.section--blog {
    margin-top: 1.5rem;

    &__slider {
        margin: 0 1rem;

        .blog--item {
            margin-bottom: 20px;
            &__img {
                position: relative;
                height: 283px;

                img {
                    width: 100%;
                    // min-height: 285px;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                    display: block;
                    background: #000000;
                }

                a {
                    position: absolute;
                    bottom: 1%;
                    left: -3%;
                    @include min-w-h(88, 29);
                }
            }

            p {
                font-size: 22px;
                font-weight: 700;
                text-transform: lowercase;
                line-height: 1.364;
                margin: 1rem 0 0 0;
                color: #000;
                &:hover{
                    color: $base-color;
                }
            }

            span {
                font-size: 19px;
                text-transform: capitalize;
                color: #646464;
                font-weight: 700;
            }
            @media screen and(max-width: $small-tablet){
                p,span{
                    display: block;
                    text-align: center;
                }
            }
        }

        .blog--item:nth-last-child(1) {
            margin-right: 0;
        }

        .slick-slide {
            outline: none;
        }
    }
}
.page-template-template-main-podcast .pagination{
    margin: 30px 0 0 0;
}