// Slider
.section--viewer-resp{
    margin-top: $margin-top ;
    @media(max-width:$tablet){
        margin-top: 0;
    }
}
.section--viewer {
    // margin-top: 108px;
    margin-top: $margin-top;
    @media (min-width: 1362px){
        max-width: 1112px;
        margin: 0 auto;
        padding: 0 15px;
    }
    &__slider {
        display: flex;
        .slick-track{
            //left: 133px;
            @media screen and(max-width: 520px){
                left: 0;
            }
        }
        .slider--img {
            position: relative;
            height: 160px;
            width: 280px !important;

            img {
                min-height: 100%;
                width: 100%;
                object-fit: cover;
            }

            a {
                @include abscen;
                font-size: 20px;
                font-weight: 700;
                color: $white-color;
                text-transform: uppercase;
                text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.75);
                z-index: 50;

                &:hover,
                &:focus {
                    outline: none;
                }
            }
            &::after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                height: inherit;
                width: 100%;
            }
            &:hover{
                &::after{ 
                    background-color: rgba($link-color,0.7);
                    // background-color: lighten($link-color, 20%);
                    // opacity: 0.2;
                }
            }
        }
    }
    @extend .custom--arrows-recetas;
    @media screen and(max-width: $tablet){
        margin-top: 0;
    }
}

// Search Form
.recipe--form {
    min-height: 160px;
    @include flexcen(center, center);

    form {
        position: relative;
        max-width: 583px;
        margin: 0 auto;
        input[type="text"] {
            min-height: 62px;
            border: 3px solid #eeede8;
            box-shadow: 0px 6px 16px 0px rgba(127, 127, 127, 0.1);
            &::placeholder {
                color: #a0a0a0;
                font-size: 17.29px;
                font-weight: 700;
                text-transform: uppercase;
            }
        }

        input[type="submit"], .fa-search {
            position: absolute;
            right: 3%;
            top: 50%;
            transform: translate(0, -50%);
            background: transparent;
        }
        input[type="submit"]{
            z-index: 50;
            padding: 10px 18px;
            &:hover +i{
                color: $link-color;
            }
        }
        .fa-search{
            font-size: 35px;
            color: $black-color;
            z-index: 20;
            padding-left: 15px;
            border-left: 1px solid #eeede8;
            transition: all 0.6s ease;
        }
    }
    @media (max-width: $small-tablet){
       form{
           max-width: 100%;
       }
    }
}

// Foods
.section--foods {
    .container{
        max-width: 1112px;
    }
    .foods--slider {
        &__item {
            // margin: 0 10px;
            max-width: 344px;
            margin: 0 auto;
            border: 1px solid transparent;
            height: 100%;
            .item__image {
                position: relative;
                height: 180px;
                padding: 0 1rem;
                background-repeat: no-repeat !important;
                background-size: cover !important;

                span {
                    @include abscen;
                    font-size: 24px;
                    text-transform: capitalize;
                    font-weight: 700;
                    color: $white-color;
                    text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.75);
                }
            }

            .item--body {
                border-left: 1px solid $gray4-color;
                border-right: 1px solid $gray4-color;
                border-bottom: 1px solid $gray4-color;
                height: calc(100% - 180px);
                position: relative;
                padding-bottom: 74px;

                &__links {
                    @include flexcen(center, center);
                    flex-direction: column;
                    padding-top: 1rem;
                    padding-bottom: 1.5rem;
                    min-height: 188px;
                    justify-content: flex-start;
                    a {
                        color: #919191;
                        text-transform: capitalize;
                        font-size: 17px;
                        font-weight: 700;
                        margin-top: 0.6rem;

                        &:hover,
                        &:focus {
                            color: $link-color;
                            outline: none;
                        }
                    }
                }

                &__button {
                    width: 80%;
                    margin: 0 auto;
                    padding: 1rem 0;
                    border-top: 1px solid #eeede8;
                    text-align: center;
                    height: 74px;
                    position: absolute;
                    bottom: 0;
                    left: 10%;
                }
            }
        }
    }

    .slick-slide {
        outline: none;
    }
}

// Form
.section--download {
    background-color: #f8f8f8;
    margin-top: 4rem;

    &__content {
        @include flexcen(center, center);
        flex-direction: column;
        min-height: 290px;

        ._form-branding {
            display: none;
        }

        ._form-content {
            ._form-label {
                display: none !important;
            }

            ._html-code {
                p {
                    font-size: 18px !important;
                    font-weight: 700;
                    font-family: Source Sans Pro, sans-serif;
                }
            }

            ._field-wrapper {
                input[type="text"] {
                    min-height: 50px !important;

                    &::placeholder {
                        color: #a3a3a3;
                        font-size: 16px;
                    }
                }
            }

            ._button-wrapper {
                margin: 0 0 0 20px !important;

                ._submit {
                    min-height: 50px !important;
                    font-weight: 700;
                }
            }

            @media screen and (max-width: $tablet) {
                ._form-title {
                    font-size: responsive(30, 19) !important;
                }

                ._html-code {
                    p {
                        font-size: responsive(18, 14) !important;
                    }
                }

                ._x57019964 {
                    input {
                        min-width: 100% !important;
                    }
                }

                ._button-wrapper {
                    ._submit {
                        width: 100% !important;
                    }
                }
            }
        }
        @media (max-width: $small-tablet){
            
            ._form_3{
                form{
                    margin-top: 0!important;
                }
            }
        }
    }
    @media (max-width: $small-tablet){
        margin-top: 2rem;
    }
}

// eBook
.section--ebook-resp{
    @media (max-width: $tablet) {
        margin-bottom: 0!important;
    }
}
.section--ebook-recipes {
    margin-top: 0;
    padding-bottom: 0;
    margin-bottom: 10px;

    .col-xl-5 {
        min-height: auto;
    }

    .section--ebook__vertical {
        left: 36%;
        img {
            min-width: auto;
            height: 570px !important;
            object-fit: cover;
        }

        p {
            font-size: 70px;
            top: 8.5%;
            // left: -180%;
            left: calc(-39vw + 50px);
        }
    }

    .section--ebook__content {
        min-height: 640px;
        @media screen and(max-width: $desktop) {
            margin-left: 40px;
        }
    }

    @media screen and(max-width: $medium-desktop) {
        background-image: linear-gradient(to bottom,
                $black-color 0,
                $black-color 45%,
                $link-color 45%,
                $link-color 100%);

        .col-xl-5 {
            min-height: 260px;
        }

        .section--ebook__vertical {
            // top: 15%;
            top: 7%;

            p {
                top: -12%;
                left: 50%;
            }

            img {
                min-height: 260px;
            }
        }
    }
    @media screen and( max-width: $small-desktop){
        .section--ebook__vertical{
            // top: 10%;
            top: 1%;
        }
    }
    @media screen and(max-width: $tablet){
        .col-xl-5 {
            min-height: 200px;
        }
        .section--ebook__vertical{
            img{
                @include responsive(min-width,350,300);
            }
        }
    }
    @media screen and(max-width: $small-tablet){
        .section--ebook__vertical{
            top: 10%;
        }
    }
}
