.social-subscribers{
    padding: 90px 0;
    .container{
        // max-width: 1250px;
        max-width: 1100px;
        padding: 0 30px;
    }
    h3{
        font-weight: 800;
    }
    .social-counter{
        &:nth-child(2){
            text-align: right;
        }
        &:nth-child(3){
            text-align: left;
        }
        &:nth-child(4){
            text-align: center;
            padding-left: 22%;
        }
        &:nth-child(5){
            text-align: center;
        }
        .social-wrap{
            display: inline-block;
            font-size: 124px;
            line-height: 45px;
            @media(max-width:$tablet){
                line-height: normal;
                font-size: 80px;
            }
            @media(max-width:$small-tablet){
                font-size: 70px;
            }
            @media(max-width:$medium-phone){
                font-size: 46px;
            }
        }
        i{
            font-size: 115px;
            @media(max-width:$tablet){
                font-size: 100px;  line-height: normal;
                font-size: 80px;
            }
            @media(max-width:$small-tablet){
                font-size: 70px;
            }
            @media(max-width:$medium-phone){
                font-size: 40px;
                padding-right: 10px;
            }
            
        }
        .number{
            color: $base-color;
            font-weight: 700;
            font-family: "Helvetica Neue", Helvetica;
        }
        .tag{
            font-size: 17px;
            text-transform: uppercase;
            font-weight: 700;
            display: block;
            text-align: right;
            color: #464646;
            @media(max-width:$medium-phone){
                font-size: 14px;
            }
        }
        @media(max-width:$tablet){
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5){
                text-align: center;
                padding-left: 0;
            }
        }
    }
    @media(max-width:$small-tablet){
        padding: 50px 0;
    }
}

.social-reach{
    background-color: $black-color;
    color: $white-color;
    padding: 60px 0 70px 0;
    .container{
        max-width: 900px;
    }
    .number{
        font-size: 120px;
        color: $base-color;
        font-weight: 700;
        font-family: "Helvetica Neue", Helvetica;
        line-height: 130px;
        @media(max-width:991px){
            line-height: normal;
            font-size: 82px;
        }
        @media(max-width:767px){
            font-size: 55px;
        }
    }
    .tag{
            font-size: 17px;
            text-transform: uppercase;
            display: block;
            text-align: right;
            color: $white-color;
            font-weight: 600;
            margin-right: 26px;
            @media(max-width:991px){
                text-align: center;
            }
    }
    .row .col-md-6:nth-child(2){
        .tag{
            margin-right: 56px;
        }
    }
    .col-12{
        .tag{
            margin-right: 0;
        }
    }
    .reach-wrap{
        display: inline-block;
        margin-bottom: 20px;
        margin-top: 10px;
        .img-number{
            display: flex;
            align-items: center;
            @media(max-width:991px){
                justify-content: center;
                margin-bottom: 15px;
            }
        }
        img{
            margin-right: 15px;
            @media(max-width:991px){
                max-width: 60px;
                margin-right: 25px;
            }
        }
        @media(max-width:991px){
            display: block;
            max-width: 400px;
            margin-left: auto;
            margin-right: auto;
        }
    }
    @media(max-width:767px){
        padding: 50px 0;
    }
}

.average-section{
    padding: 50px 0 200px 0;
    position: relative;
    h3{
        font-weight: 800;
    } 
    .box{
        text-align: center;
        border: 3px solid #e1e1e1;
        padding: 15px 25px;
        min-height: 250px;
        .number{
            font-size: 95px;
            font-weight: 700;
            font-family: "Helvetica Neue", Helvetica;
            color: $base-color;
            display: block;
            // line-height: 100px;
            line-height: 142px;
            span{
                font-size: 50px;
            }    
        }
        .tag{
            font-size: 18px;
            font-weight: 700;
            color: #464646;
            line-height: normal;
            max-width: 170px;
            display: block;
            margin: auto;
            margin-top: -42px;
            text-transform: uppercase;
        }
        @media (max-width: $small-desktop) {
            margin-bottom: 15px;
            .number{
                font-size: 89px;
            }
        }
        @media(max-width:991px){
            min-height: 220px;
            margin-bottom: 15px;
        }
        
    }
    @media screen and (max-width: $small-desktop){
        padding-bottom: 50px;
    }
}
.red-box{
    max-width: 985px;
    width: 100%;
    // margin: 55px auto;
    border: 3px solid $base-color;
    padding: 35px 40px;
    background-color: $white-color;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 50%);
    h3{
        margin-bottom: 30px;
        // font-weight: 800;
    }
    p{   
        position: relative;
        padding-left: 56px;
        margin-bottom: 30px;
        font-weight: 700;
        &::before{
            position: absolute;
            content: "";
            top: 50%;
            left: 0px;
            min-width: 40px;
            min-height: 40px;
            background: url(../../images/arrow-list.png) no-repeat center center/contain;
            color: var(--color-pink-second);
            transform: translateY(-50%);
        }
        @media screen and (max-width: $medium-phone){
            margin-left: 28px;
        }
    }
    @media screen and (max-width: $small-desktop){
        position: relative;
        left: 0;
        transform: none;
    }
}
