.work-with-section{
    background-color: #f8f8f8;
    padding-bottom: 115px;
    padding-top: 200px;
    text-align: center;
    form{
        label{
            display: inline-flex;
            align-items: center;
            font-weight: 700;
            margin-bottom: 15px;
        }
        .wpcf7-form-control-wrap{
            // padding-left: 15px;
        }
        p{
            margin-bottom: 0;
        }
    }
    h3,h4{
        margin-bottom: 10px;
        font-weight: 900;
    }
    h3{
        color: $link-color;
        font-size: 30px;
    }
    h4{
        font-size: 21px;
        text-transform: initial;
    }
    .contact--media{
        .wpcf7-form{
            p:nth-child(6){
                display: flex;
                justify-content: flex-end;
                margin-right: -20px;
                margin-top: -2px;
                @media (max-width: $small-tablet){
                    margin-right: 22px;
                }
                @media screen and (max-width: $small-phone){
                    margin-right: 0;
                    .ajax-loader{
                        width: 0;
                    }
                }
            }
        }
    }
    @media screen and (max-width: $small-desktop){
        padding-top: 50px;
    }
}