/* Media Queries
---------------------------------------------------------------------------- */

//@media only screen and (min-width: 992px)
@media only screen and (min-width: $tablet) {
    /* Site Header
	--------------------------------------------- */
    .site-header {
        width: 100%;
        // position: absolute;
        // position: fixed;
        position: sticky;
        top: 0;
        z-index: 1000;
    }

    .admin-bar .site-header {
        // top: 32px;
    }

    /* Genesis Menu
	--------------------------------------------- */
    .genesis-nav-menu .menu-item {
        display: inline-block;
    }

    .genesis-nav-menu .menu-item:focus,
    .genesis-nav-menu .menu-item:hover {
        position: relative;
    }

    .genesis-nav-menu > .menu-bold > a {
        font-weight: 700;
    }

    .genesis-nav-menu > .menu-highlight > a {
        background-color: #333;
        border-radius: 3px;
        color: #fff;
        font-weight: 600;
        margin-left: 15px;
        padding-left: 20px;
        padding-right: 20px;
    }

    .genesis-nav-menu > .menu-highlight > a:focus,
    .genesis-nav-menu > .menu-highlight > a:hover {
        background-color: #0073e5;
    }

    .genesis-nav-menu .sub-menu,
    .genesis-nav-menu .sub-menu a {
        // width: 180px;
        width: 170px;
    }

    .genesis-nav-menu .sub-menu {
        border-top: 1px solid #eee;
        opacity: 0;
        padding-left: 0;
        position: absolute;
        // animation: growOut 350ms ease-in-out forwards;
        transform-origin: 30%!important;
        @keyframes growOut {
            0% {
                transform: scale(0.2);
                // transform: translateY(-40%) scale(.2);
            }
            25% {
                transform: scale(0.25);
            }
            50% {
                transform: scale(0.5);
            }
            75% {
                transform: scale(0.75);
            }
            100% {
                transform: scale(1);
            }
        }
}

    .genesis-nav-menu .sub-menu a {
        // border: 1px solid #eee;
        border-top: 0;
        // padding-bottom: 15px;
        // padding-top: 15px;
        padding-bottom: 12px;
        padding-top: 12px;
    }

    .genesis-nav-menu .sub-menu .sub-menu {
        margin: -46px 0 0 179px;
    }

    /* Responsive Menu
	--------------------------------------------- */
    .genesis-responsive-menu {
        display: block;
        // padding-top: 25px;
        padding-top: 18px;
    }

    .menu-toggle,
    .sub-menu-toggle {
        display: none;
        visibility: hidden;
    }

    /* Header Menu
	--------------------------------------------- */
    .nav-primary {
        clear: none;
        float: right;
        width: auto;
    }

    /* Site-Inner
	--------------------------------------------- */
    .site-inner {
        max-width: 1190px;
    }

    /* Content
	--------------------------------------------- */
    .content {
        float: left;
        width: 65%;
    }

    .sidebar-content .content {
        float: right;
    }

    .full-width-content .content {
        float: none;
        margin-left: auto;
        margin-right: auto;
    }

    /* Sidebar
	--------------------------------------------- */
    .sidebar {
        float: right;
        width: 30%;
    }

    .sidebar-content .sidebar {
        float: left;
    }

    /* Author Box
	--------------------------------------------- */
    .author-box {
        background-color: #f5f5f5;
        padding: 30px;
    }

    /* After Entry
	--------------------------------------------- */
    .after-entry {
        padding: 40px 60px;
    }

    .after-entry .enews {
        padding-left: 30px;
        padding-right: 30px;
    }

    /* Column Classes
	--------------------------------------------- */
    .five-sixths,
    .four-sixths,
    .one-fourth,
    .one-half,
    .one-sixth,
    .one-third,
    .three-fourths,
    .three-sixths,
    .two-fourths,
    .two-sixths,
    .two-thirds {
        float: left;
        margin-left: 2.564102564102564%;
    }

    .one-half,
    .three-sixths,
    .two-fourths {
        width: 48.717948717948715%;
    }

    .one-third,
    .two-sixths {
        width: 31.623931623931625%;
    }

    .four-sixths,
    .two-thirds {
        width: 65.81196581196582%;
    }

    .one-fourth {
        width: 23.076923076923077%;
    }

    .three-fourths {
        width: 74.35897435897436%;
    }

    .one-sixth {
        width: 14.52991452991453%;
    }

    .five-sixths {
        width: 82.90598290598291%;
    }

    .first {
        clear: both;
        margin-left: 0;
    }

    /* Entry Misc.
	--------------------------------------------- */
    .after-entry,
    .archive-description,
    .author-box,
    .comment-respond,
    .entry,
    .entry-comments,
    .entry-pings {
        margin-bottom: 60px;
    }

    /* Footer Widgets
	--------------------------------------------- */
    .footer-widgets .wrap {
        max-width: 1140px;
    }

    .footer-widget-area {
        float: left;
        margin-bottom: 0;
        width: calc(100% / 3);
    }
}
