.section--featured {
    .hero-videos{
        position: relative;
        overflow: hidden;
    }
    .animation--background{
        background-image: url('../../images/video-bg.jpg');
        @media(max-width: $phone){
            background-position: center;
        }
    }
    &.videos{
        // padding-top: 106px;  
        padding-top: $margin-top;
        position: relative;
        background-color: #c10001;
        .section--featured__content{
            a{
                color: #fff;
            }
            h5{
                text-transform: initial;
            }
        }
        .section--featured__image{
            position: relative;
            margin-left: 0;
            max-width: 556px;
            .video-bg{
                // width: 565px;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }
        @media (max-width: $tablet){
            // padding-top: 26px;
            padding-top: 34px;
        }
        
    }
}
.section--filter__videos{
    ul{
        li li {
            label{
                padding-top: 5px!important;
            }
            &:nth-child(1) {
                label::before {
                    content: ""!important;
                }
            }
            &:nth-child(2) {
                label::before {
                    content: ""!important;
                }
            }
            &:nth-child(3) {
                label::before {
                    content: ""!important;
                }
            }
        }
        .sf-field-search{
            position: relative;
            flex: 1;
            ::before{
                content:'';
                @include abs-position(27px, 0, auto, auto);
                width: 32px;
                height: 32px;
                background-image: url('../../images/post/search.png');
            }
            label{
                width: 100%;
                input{
                    border: none;
                }
                input::placeholder{
                    color: #b6b6b6 ;
                }
            }
            
        }
        @media (max-width: $tablet){
            display: revert;
        }
        @media (max-width: $medium-phone){
            &>li>ul:not(.children) {
                display: block;
                
            }
        }
    }
    .sf-field-taxonomy-video-category:not(:first-of-type) {
        display: none;
        margin-top: 25px;
        select{
            outline: none;
        }
        @media screen and(max-width: $small-tablet){
            display: flex;
            align-items: center;
            justify-content: center;
            h4{
                display: none;
            }
            label{
                width: 100%;
            }
        }
    }
    @media screen and(max-width: $small-tablet){
        .sf-field-taxonomy-video-category:first-child{
            display: none;
        }
    }
}
.section--videos{
    .section--videos__items{
        position: relative;
        p{
            text-transform: capitalize;
        }
        .section--videos__img{
            position: relative;
            height: 215px;
            &__link{
                width: 100%;
                height: 100%;
              
            }
            a{
                left: 0;
            }
            .icon--video-button{
                width: 42px;
                height: 42px;
                background: transparent;
            }
        }
        .bg-video{
            // width: 380px;
            height: 100%;
            object-fit: cover!important;
            object-position: center!important;
           // background: transparent!important;
           @media (max-width: $small-tablet){
               max-width: 100%;
           }
        }
        a{
            bottom: auto;
            min-width: auto;
            min-height: auto;
        }

    }
}