/* Footer Widgets
---------------------------------------------------------------------------- */

.footer-widgets {
    border-top: 1px solid #eee;
    background-color: #fff;
    clear: both;
    padding: 60px 0;
}

.footer-widgets .wrap {
    margin-left: auto;
    margin-right: auto;
}

.footer-widget-area {
    margin-bottom: 40px;
    padding-left: 30px;
    padding-right: 30px;
}

.footer-widget-area:last-child,
.footer-widgets .widget:last-child {
    margin-bottom: 0;
}

/* Site Footer
---------------------------------------------------------------------------- */

.site-footer {
    min-height: 475px;
    background-color: $black-color;
    font-size: 15px;
    line-height: 1.5;
    text-align: center;

    .container {
        position: relative;
    }

    &__logo {
        margin-top: 3rem;
    }

    &__menu {
        margin-top: 2.5rem;

        ul {
            @include flexcen(center, center);
            flex-wrap: wrap;

            a {
                font-size: 18px;
                text-transform: uppercase;
                color: $white-color;
                font-weight: 700;
                margin: 0 2rem;

                &:hover {
                    color: $link-color;
                }
            }
            @media screen and(max-width: $tablet){
                a{
                    margin: 0 1rem;  
                }
            }
            @media screen and(max-width: $small-tablet){
                a{
                    margin: 0 0.5rem;  
                }
            }
            @media screen and(max-width: $phone){
                li{
                    width: 100%;
                }
            }
        }
    }

    &__socials {
        margin-top: 2.5rem;

        ul {
            @include flexcen(center, center);

            a {
                font-size: 28px;
                color: $white-color;
                margin: 0 2rem;

                .fab {
                    transition: all 0.5s ease;
                }


                &:hover {
                    color: $link-color;

                    .fab {
                        transform: scale(1.4);
                    }
                }
            }
        }
    }

    &__copy {
        color: $white-color;
        margin-top: 2.5rem;
        text-transform: uppercase;
        font-weight: 700;
    }

    &__scale {
        position: absolute;
        right: 2%;
        bottom: -12px;
        // width: 97px;
        width: 50px;
        @media screen and (max-width: $phone) {
            @include responsive(width, 97, 50);
        }

        @media screen and(max-width: $tablet) {
            right: 50%;
            bottom: -70px;
            transform: translate(50%, 0);
        }
    }
    @media screen and (max-width: $phone){
        min-height: 537px;
    }
}