/*Hero*/
.hero--contact{
    min-height: 426px;
    margin-top: $margin-top;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    .animation--background{
        background-position: 87%;
        background-size: cover;
    }
    &__content{
        position: relative;
        z-index: 1;
        h1{
            font-weight: bold;
            @include font-responsive(61, 28);
            color: $white-color;
            border-top: 4px solid $white-color;
            border-bottom: 4px solid $white-color;
            padding: 25px 0 30px
        }
        
    }
    @media screen and (max-width: $tablet){
        margin-top: 0;
    }
    @media screen and (max-width: $small-phone){
        &__content{
            h1{
                padding: 18px 0 18px;
                margin-top: 94px;
            }
        }
    }
}

/*Section Form*/
.contact--form{
    background-color: #fff2f2;
    text-align: center;
    padding: 50px 0 80px;
    &__text{
        h4{
            @include font-responsive(30.72, 18);
            margin-bottom: 18px;
        }
        h2{
            @include font-responsive(55.25, 23);
            margin-bottom: 34px;
            span{
                padding: 0px 15px 2px;
                background-color: $base-color;
                color: $white-color;
            }
        }
        p{
            max-width: 555px;
            margin: 0 auto;
            @include font-responsive(21, 18);
            line-height: 30px;
            font-weight: bold;
        }
    }
    &__contact{
        max-width: 720px;
        margin: 0 auto;
        padding-top: 38px;
        .wpcf7-form{
            label{
                display: flex;
                align-items: center;
                justify-content: flex-end;
                text-transform: uppercase;
                font-weight: bold;
                @include font-responsive(15.34, 13);
                input,
                select,
                textarea{
                    width: 598px;
                    margin-left: 19px;
                }
                
            }
            p{
                margin-bottom: 17px;
            }
            p:nth-child(7){
                display: flex;
                justify-content: flex-end;
                margin-right: -20px;
                margin-top: -2px;
            }
            input[type="submit"]{
                width: 598px;
                background-color: #88ff9c;
                color: $black-color;
                text-transform: uppercase;
                @include font-responsive(21, 18);
                font-weight: bold;

            }
            .wpcf7-response-output{
                width: 598px;
                margin: 2em 0 0 auto;
                border-color: $base-color !important;
            }
        }
        @media screen and(max-width: $tablet){
            .wpcf7-form{
                label{
                    input,
                    select,
                    textarea{
                        width: responsive(598, 250, 1000, 375);
                    }
                    .wpcf7-form-control-wrap{
                        width: 80%;
                    }
                }
                p:nth-child(7){
                    margin-right: 20px;
                }
                input[type="submit"]{
                    width: responsive(598, 250, 1000, 375);
                }
                .wpcf7-response-output{
                    width: responsive(598, 250, 1000, 375);  
                    margin-right: 40px;
                }
            }
        }
        @media screen and(max-width: $small-phone){
            padding-top: 50px;
            .wpcf7-form{
                label{
                    flex-direction: column;
                    align-items: flex-start;
                    input,
                    select,
                    textarea{
                        width: 100%;
                        margin-left: 0;
                    }
                    .wpcf7-form-control-wrap{
                        width: 100%;
                        margin-top: 15px;
                    }
                }
                p:nth-child(7){
                    margin-right: -20px;
                }
                input[type="submit"]{
                    width: 100%;
                }
                .wpcf7-response-output{
                    width: 100%;
                }
            }
        }
    }
}