// Menu Recipe
.recipe--menu {
    .row {
        background-color: $gray2-color;
        min-height: 63px;
        font-family: $font-recipe-menu;
    }
    a{
        color: $gray5-color;
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        margin-right: 15px;
        i {
            font-size: 18px;
            position: relative;
            top: 2px;
            margin-right: 10px;
        }
        span {
            color: $link-color;
            text-transform: capitalize;
            font-size: 25px;
            font-weight: 900;
        }
    }
    &__social{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        a {
            //font-family: $font-title-recipe;
            &:last-child{
                line-height: 2;
            }
            img {
                margin-right: 10px;
            }
        }
        form{
            position: relative;
            top: 6px;
            margin-bottom: 0;
            input[type="submit"]{
                background-color: transparent;
                position: relative;
                z-index: 10;
                height: 0;
            }
            img{
                position: absolute;
                left: 0;
            }
        }
        
    }
    @media screen and (max-width: $tablet){
        &__social{
            justify-content: center;
        }
        .row{
            padding: 10px 0;
        }
    }
    
}

// Recipe Ingredients
.recipe--ingredients {
    margin-top: 50px;

    .container {
        padding-left: 0;
    }

    &__slider {
        margin-bottom: 50px!important;
        .slider--item {
            min-height: 455px;
            position: relative;
            @include bg-image("../images/receta/receta-image.jpg");

            a {
                position: absolute;
                top: 3%;
                right: 3%;
                outline: none;
            }
        }
    }

    @extend .custom--dots-receta;

    .slick-slide {
        outline: none;
    }

    &__categories {
        a {
            margin: 0 3px;
        }
    }

    &__list {
        margin-top: 18px;
        margin-left: 10px;

        &>span {
            display: block;
            font-size: 20px;
            font-weight: 700;
            color: $gray5-color;
            margin-bottom: 16px;
        }

        .list--item {
            // @include flexbox;
            margin-bottom: 16px;
            position: relative;
            padding-left: 26px;

            // &__icon {
            //     width: 8px;
            //     height: 8px;
            //     background-color: $link-color;
            //     border-radius: 30%;
            //     margin: 8px 20px 0 10px;
            // }
            &::before{
                content: "";
                position: absolute;
                width: 8px;
                height: 8px;
                background-color: #be0712;
                border-radius: 3px;
                left: 6px;
                top: 6px;
                z-index: 10;
            }         

            &__content {
                p {
                    font-size: 16px;
                    font-weight: 700;
                    color: $gray5-color;
                    line-height: 1.25;
                    margin-bottom: 0px;

                    span {
                        color: $link-color;
                    }
                }
            }
        }
    }
    @media screen and( max-width: $small-tablet){
        .container{
            padding-left: 15px;
        }
        &__list{
            &>span{
                text-align: center;
            }
        }
    }
}

// Directions
.section--directions {
    .container {
        background-color: #fcfcfc;
        padding-top: 20px;
        padding-bottom: 20px;
        border: 2px solid rgb(234, 234, 234);
    }

    h6 {
        font-size: 20px;
        color: $gray5-color;
        text-transform: capitalize;
        margin-bottom: 0;
        margin-left: 20px;
        line-height: 2;
    }

    &__time {
        @include flexbox;
        max-width: 245px;
        background-color: $white-color;

        .time--item {
            padding: 0 10px;

            &__clock {
                @include flexbox;
                align-items: center;
                padding-right: 15px;

                img {
                    margin-right: 10px;
                }
            }

            p {
                font-size: 15.6px;
                color: #b4b4b4;
                font-weight: 700;
                margin: 0px;
                padding: 0;
            }

            span {
                display: block;
                color: $link-color;
                margin-top: -6px;
            }
        }

        .time--item:nth-child(2) {
            padding: 0 20px;
            //border-right: 1px solid #e9e9e9;
            border-left: 1px solid #e9e9e9;
        }
    }

    &__list {
        margin-top: 30px;

        .list--item {
            display: flex;
            // align-items: center;
            height: 106%;
            min-height: 70px;
            margin-bottom: 10px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: inherit;
                background-color: $link-color;
                left: 43px;
                top: 15%;
                z-index: 5;
            }

            &__circle {
                min-width: 90px;
                min-height: inherit;
                position: relative;
                z-index: 10;

                .circle--red {
                    width: 49px;
                    height: 50px;
                    border-radius: 50%;
                    border: 3px solid $link-color;
                    background-color: #fcfcfc;
                    margin: 0 20px;
                    text-align: center;
                    line-height: 2.2;
                    font-size: 20px;
                    color: $link-color;
                    font-weight: 700;
                }
            }

            &__content {
                align-self: flex-start;

                p {
                    margin-bottom: 0;
                    font-size: 16px;
                    color: $gray5-color;
                    font-weight: 600;
                    line-height: 1.25;
                }
            }
            @media screen and(max-width: $tablet){
                // min-height: calc(200px - 10vw);
            }
        }

        .list--item:nth-last-child(1) {
            &::after {
                min-height: 0;
                height: 0;
            }
        }
    }
    @media screen and(max-width: $small-tablet){
        &__time{
            margin: auto;
        }
        &__list{
            .list--item{
                &::after{
                    left: 23px;
                }
                &__circle{
                    min-width: auto;
                    .circle--red{
                    margin: 0 20px 0 0;
                }
            }
            }
        }
    }
}

// Councils
.section--councils {
    padding-top: 40px;

    .container {
        padding-left: 0;
    }

    h6,
    p {
        color: $gray5-color;
    }

    h6 {
        font-size: 20px;
        font-weight: 700;
        text-transform: capitalize;
    }

    p {
        font-size: 16px;
        font-weight: 600;
        line-height: 1.25;
    }
    @media screen and(max-width: $desktop){
        .container{
            padding-left: 40px;
        }
        h6{
            text-align: center;
        }
    }
    @media screen and(max-width: $tablet){
        .container{
            padding: 0 30px;
        }
        p{
            text-align: justify;
        }
    }
}
.foods--slider__item-hover{
    border: 1px solid transparent; 
    &:hover{
        border: 1px solid $link-color;    
        box-shadow: 0 3px 24px 0 rgba(0,0,0,.23);
        background-color: #fff;
    }
}