// Eventos Online 
.events--online{
    margin-bottom: 80px;
    &__head{
        max-width: 632px;
        margin: 0 auto;
        text-align: center;
        padding: 37px 0 15px;
        p{
            @include font-responsive(18, 16);
            line-height: 30px;
            font-weight: 600;
        }
        h2{
            color: $link-color;
            @include font-responsive(30, 22);
        }
    }
    &__card{
        padding: 16px;
        text-align: center;
        border: 2px solid #eaeaea;
        height: 100%;
        box-shadow: 0px 0px 25px 0px rgba(238, 238, 238, 0.82);
        transition: all .3s ease;
        position: relative;
        &__image{
            background-repeat: no-repeat;
            min-height: 162px;
        }
        &__text{
            // padding: 0 28px;
            h2{
                max-width: 251px;
                margin: 30px auto 20px;
                color: $link-color;
                @include font-responsive(30, 22); 
                line-height: 31px;
            }
            p{
                @include font-responsive(18, 16);
                line-height: 30px;
                font-weight: 600;
            }
        }
        &:hover{
            transform: translateY(-12px);
            border: 2px solid $base-color;
            

        }

        
    }
    
    @media (max-width: $tablet){
        .col-md-6{
            margin-bottom: 40px;
        }
    }
    @media (max-width: $small-tablet){
        margin-bottom: 40px;
        
        &__head{
            p{
                line-height: 24px;
            }
        }
        &__card{
        max-width: 400px;
        margin: 0 auto;
            &__text{
                h2,p{
                    line-height: 24px;
                }
            }
        }
    }
}

// Event Person 
.events--person{
    &__header{
        h2{
            color: $link-color;
            @include font-responsive(30, 22);
        }
    }
    &__card{
        padding: 19px;
        border: 1px solid #eaeaea;
        box-shadow: 0px 0px 25px 0px rgba(238, 238, 238, 0.82);
        &__image{
            background-repeat: no-repeat;
            background-size: cover;
            min-height: 281px;
        }
        &__text{
            margin: 16px 0;
            &__date{
                display: inline;
                background-color: $base-color;
                color: $white-color;
                padding: 3px 16px;
                text-transform: uppercase;
                border-radius: 10px;
                font-weight: 700;
            }
            h2{
                margin: 12px 0 10px;
                color: $link-color;
                @include font-responsive(30, 22);
            }
            p{
                @include font-responsive(18, 16);
                line-height: 30px;
                font-weight: 600;
                margin-bottom: 16px;
            }
            &__location{
                padding-left: 39px;
                position: relative;
                &::before{
                    content: '';
                    position: absolute;
                    @include abs-position(-3px, auto, auto, 0);
                    width: 24px;
                    height: 36px;
                    background-image: url('../../images/map-marker.png');
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }
        }
        @media (max-width: 767px){
            max-width: 100%;
            margin: 0 auto;
            &__text{
                p{
                    line-height: 24px;
                }
            }
            
        }
        @media (max-width: $medium-phone){
            &__image{
                min-height: 200px;
            }
            &__text{
                text-align: center;
                &__location{
                    max-width: 401px;
                    margin: 0 auto;
                }
            }
        }
    }
}