//Hero
.hero-podcast{
    background-size: cover;
    background-position: center;
    background-color: #be0712;
    min-height: 427px;
    margin-top: $margin-top;
    display: flex;
    align-items: center;
    h1{
        text-align: center;
        @include font-responsive(55, 30);
        span{
         background-color: $base-color;
         color: $white-color;
         padding: 10px 19px;
         line-height: 40px;
        }
    }
    @media (max-width: $tablet ){
        margin-top: 0;
        h1{
            span{
                display: block;
            }
        }
    }
    @media (max-width: $small-tablet){
        min-height: 350px;
        background-position: 25%;
    }
}
// section--subscribe
.section--subscribe{
    .container{
        max-width: 1220px;
    }
    &__content{
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 3px solid $black-color;
        padding: 23px 0;
        
        h5{
            margin: 0 12px 0 0;
            @include font-responsive(21, 18);
        }
        ul{
            li{
                display: inline-block;
                padding-left: 8px;
                img{
                    max-width: 38px;
                    transition: all .25s linear;
                    &:hover{
                        display: inline-block;
                        transform: translateY(-5px);
                    }
                }
                
            }
        }
        @media (max-width: $medium-phone){
            flex-direction: column;
            h5{
                margin-bottom: 20px;
            }
            ul{
                display: flex;
                li{
                    display: block ;
                }
            }
        }
    }
}

// section-invited 
.section-invited{
    .container{
        max-width: 1220px;
    }
    padding-top: 35px;
    h4{
        @include font-responsive(30, 20);
        margin-bottom: 30px;
    }
    p{
        @include font-responsive(18, 16);
        font-weight: 600;
    }
    ul{
        margin-bottom: 46px;
        li{
            padding-left: 62px;
            position: relative;
            margin-bottom: 30px;
            @include font-responsive(18, 16);
            font-weight: 600;
            &::before{
                content: '';
                @include abs-position(-6px, auto, auto, 0);
                width: 38px;
                height: 38px;
                background-image: url('../../images/arrow-list.png');
                background-size: contain;
                background-repeat: no-repeat;
            }
            a{
                color: $black-color;
                margin-left: 5px;
                font-weight: 700;
            }
        }
    }
    @media (max-width: $medium-phone){
        padding-top: 25px;
        h4,p{
            margin-bottom: 20px;
        }
        ul{
            margin-bottom: 30px;
            li{
                padding-left: 50px;
                margin-bottom: 20px;
                &::before{
                    @include abs-position(0px, auto, auto, 0);
                }
            }
        }
    }
}