.error404{
    .site-inner{
        max-width: 100%;
    }
    .content{
        width: 100%;
    }
    article{
        margin-bottom: 0;
        .container{
            max-width: 50%;
            padding: 30px 15px 40px;
            text-align: center;
            h1{
                text-align: center;
            }
            h6{
                text-align: center;
            }
            form{
                width: 80%;
                margin: auto;
                input[type=submit] {
                    // width: 100%;
                    min-width: 204px;
                    max-height: 50px;
                    font-weight: 700;
                    font-size: 14px;
                    text-transform: uppercase;
                    padding: 18px;
                    background-color: $link-color;
                }
               .search-form-input::placeholder{
                   color: transparent;
               }
            }
            @media screen and(max-width: $tablet){
                max-width: 100%;
                form{
                    width: 90%;
                    input[type=submit] {
                        min-width: 100%;
                    }

                }
            }
        }
    }
    .section--download{
        margin-top: 0;
    }
}