
.page-template-template-bigraphy{
    .section--download{
        margin-top: 0;
    }
}
// Biography Hero 
.animation--background{
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: top right;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all .6s ease;
    &:after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @media screen and(max-width: $tablet){
            background: #fff;
            opacity: 0.5;
        }
    }
}
.biography--hero{
    margin-top: $margin-top;
    min-height: 489px;
    background-color: $base-color;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    &__content{
        h2{
            text-transform: initial;
            color: $link-color;
            @include font-responsive(35, 22);
            font-weight: 900;
        }
        p{
            @include font-responsive(21, 18);
            font-weight: 800;
        }
        
    }
    @media (max-width: $tablet ){
        margin-top: 0;
        .animation--background{
            background-position-x: 80%;
        }
    }
}

// Section History 
.section--history{
    padding: 63px 0 50px;
    &__text{
        margin-top: 70px;
        .history--animation{
            display: flex;
            .history--line{
                width: 4px;
                height: 85px;
                background-color: $base-color;
                position: relative;
                transition: all 0.3s ease;
                @media screen and(max-width: $phone){
                    height: 60px;
                }
            }
            .history--line2{
                height: 50px;
            }
            h2{
                padding-left: 20px;
                max-width: 445px;
                text-transform: initial;
                color: $black-color;
                @include font-responsive(35, 22);
                margin-bottom: 40px;
                position: relative;
                // &:before{
                //     content: '';
                //     @include abs-position(0px, auto, auto, 0);
                //     width: 4px;
                //     height: 100%;
                //     background-color: $base-color;
                // }
            }
        }
        .history--animation1{
            transition: all 0.5s ease;
        }
        .history--animation2{
            transition: all 0.5s ease;
        }
        
        p{
            @include font-responsive(19, 16);
            line-height: 30px;
            font-weight: 600;
        }
        &.history2{
            margin-top: 0px;
        }
    }
    
    &__image{
        padding-right: 10px;
        &--bg{
            min-height: 544px;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }
    }
    @media (max-width: $small-tablet ){
        padding-bottom: 0;
        &__text{
            margin-top: 0;
        }
        &__image{
            padding-right: 0;
        }
    }
    @media (max-width: $small-phone ){
        &__image{
            &--bg{
                min-height: 420px;
                background-size: cover;
            }
        }
    }
}

// Section About
.section--biography{
    background-color: $white-color;
    &__header{
        max-width: 620px;
        margin: 0 auto;
        h3{
            text-align: center;
            text-transform: initial;
            position: relative;
            z-index: 1000;
            margin-bottom: 110px;
        }
        @media (max-width: $medium-desktop){
            h3{
                margin-bottom: 30px;
            }
        }
    }
    p{
        @include font-responsive(19, 16);
        font-weight: 400;
        text-align: left;
    }
    .image{
        &.image--fruit2{
            top: 193px;
            width: 480px;
            left: calc(50% - 520px);
            // left: 10%;
            // img{
            //     max-width: 400px;
            //     margin-top: -150px;
            // }
        }
        &.image--vegetal2{
            top: 660px;
            left: 22%;
        }
        &.image--eating2{
            top: 460px;
        }
    }
    .section--about__content{
        min-height: 454px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    @media (max-width: $medium-desktop){
        .section--about__content{
            p:nth-child(2){
                margin-top: 30px;
            }
        }
        .section--about__content{
            min-height: auto;
        }
    }
    
    @media (max-width: $small-tablet){
        .section--about__content{
            p:nth-child(2){
                margin-top: 30px;
            }
        }
    }
    //me custom
    .section--about__images{
        .row{
            justify-content: center;
            .text-center{
                &:last-child(){
                    margin-top: 15px;
                }
                @media (max-width: $small-tablet){
                    &:nth-of-type(2n){
                        margin: 15px auto;
                    }
                }
            }
        }
    }      
}
// Bigraphy Video 
.biography--video{
    min-height: 583px;
    position: relative;
    video{
        position: absolute;
        background-color: $base-color;
        min-width: 100%;
        height: 100%;
        object-fit: cover;
    }
    @media only screen and (min-width: $big-desktop) {
        // padding-top: 4% !important;
        margin-bottom: 4%;
    }
    @media only screen and (min-width: $medium-desktop) {
        padding-top: 5%;
        margin-bottom: 5%;
    }
    @media only screen and (max-width: $small-tablet ) {
       min-height: 400px; 
    }
    @media only screen and (max-width: $medium-phone ) {
       min-height: 300px; 
    } 
}