// Arrows
.custom--arrows {

    .slick-prev,
    .slick-next {
        height: 80px;
        width: 80px;
        z-index: 500;
        top: 82.5%;

        .fa {
            color: white;
            font-size: 30px;
        }
    }

    .slick-prev {
        left: calc(50vw - 115px);

        &:before {
            content: "";
        }
    }

    .slick-next {
        right: calc(50vw - 105px);

        &:before {
            content: "";
        }
    }
}

// Dots
.custom--dots {
    .slick-dots {
        bottom: 28%;
        display: block;

        li {
            button:before {
                opacity: 1;
                color: transparent;
                font-size: 11px;
                opacity: 1;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                border: 2px solid $white-color;
            }

            &.slick-active button:before {
                content: "";
                background-color: $white-color;
            }
        }
        @media screen and ( max-width: $tablet){
            // bottom: 33.5%;
            bottom: 50px;
        }
    }
}

// Testimonial
.custom--dots-test {
    .slick-dots {
        bottom: 10px;
        display: block;

        li {
            margin: 0 5px;

            button:before {
                content: "";
                opacity: 1;
                color: transparent;
                font-size: 35px;
                width: 13px;
                height: 14px;
                background-color: $black-color;
            }

            &.slick-active button:before {
                background-color: $link-color;
                width: 40px;
                transition: all 0.5s ease-in;
            }
        }

        .slick-active {
            margin-left: 5px;
            margin-right: 30px;
        }
    }
}

// Arrows Recetas Page
.custom--arrows-recetas {

    .slick-prev,
    .slick-next {
        height: 52px;
        width: 32px;
        z-index: 500;
        top: 50%;
        background-color: $link-color;
        border-radius: 0;

        .fa {
            color: $white-color;
            font-size: 30px;
        }
    }

    .slick-prev {
        left: 10%;

        &:before {
            content: url(../../images/arrow-left.png);
            opacity: 1;
        }
    }

    .slick-next {
        // right: 16%;
        // right: 0;
        // $btn : responsive(310, 48, 1440, 375);
        // left: calc(48px + (310 - 48)*((100vw - 375px)/(1400 - 375)));
        // right: responsive(310, 15, 1440, 992);
        right: 11%;

        &:before {
            content: url(../../images/arrow-right.png);
            opacity: 1;
        }
    }

    .slick-slide {
        outline: none;
    }
    @media screen and (max-width: $tablet){
        .slick-prev{
            left: 22%;
        }
        .slick-next{
            right: 23%;
        }
    }
    @media screen and (max-width: $phone){
        .slick-prev{
            left: 10%;
        }
        .slick-next{
            right: 10%;
        }
    }
}

// Receta
.custom--dots-receta {
    .slick-dots {
        bottom: 25px;
        display: block;

        li {
            margin: 0 5px;
            // button{
            //     width: 15px;
