/* Site Header
---------------------------------------------------------------------------- */

.site-header {
    background-color: #000;
    // position: fixed !important;
    transition: all 0.2s ease;
    // box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
    padding: 0 30px;
    width: 100%;
    z-index: 1000;

    .wrap {
        // max-width: 1460px;
        max-width: 1312px;
        margin: 0 auto;
    }
    @media screen and(max-width: $tablet){
        // position: absolute !important;
    }
}



/* Title Area
--------------------------------------------- */

.title-area {
    float: left;
    // padding-bottom: 12px;
    // padding-top: 12px;
    padding-bottom: 8px;
    padding-top: 8px;

    a img {
        width: auto;
        // max-width: 120px;
        max-width: 90px;
    }

    .main--logo {
        display: none;
    }
}

.wp-custom-logo .title-area {
    max-width: 350px;
    padding-bottom: 5px;
    padding-top: 5px;
    width: 100%;
}

.site-title {
    font-size: 20px;
    font-weight: 600;
    line-height: 1;
    margin-bottom: 0;
}

.site-title a,
.site-title a:focus,
.site-title a:hover {
    color: #333;
    text-decoration: none;
}

.site-description,
.wp-custom-logo .site-title {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    position: absolute !important;
    width: 1px;
    word-wrap: normal !important;
}


/* Site Navigation
---------------------------------------------------------------------------- */

.genesis-nav-menu {
    clear: both;
    line-height: 1;
    width: 100%;
}

.nav-primary .genesis-nav-menu > li a {
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 6px;
}
.nav-primary .genesis-nav-menu .sub-menu a {
    border-radius: 0;
}

.genesis-nav-menu .menu-item {
    display: block;
    float: none;
    position: relative;

    &.menu-item-has-children{
        // padding-bottom: 20px;
        padding-bottom: 10px;
    }
    &.menu-item-has-children>a {
        position: relative;
        padding-right: 40px;
        border-radius: 6px;

        :after {
            content: "\f078";
            color: $base-color;
            font-family: "Font Awesome 5 Free";
            /* This is the correct font-family*/
            font-style: normal;
            font-weight: 900;
            font-size: 13px;
            position: absolute;
            right: 20px;
            margin-top: 2px;

            @media screen and(max-width: $tablet) {
                content: "";
            }
        }

        &:hover {
            :after {
                color: $white-color;
            }
        }
    }

    &.current-menu-item {
        a {
            :after {
                color: $white-color;
                
            }
            
        }
    }
}

.genesis-nav-menu a {
    color: $white-color;
    display: block;
    text-transform: uppercase;
    // font-size: 18px;
    font-size: 15px;
    font-weight: 700;
    outline-offset: -1px;
    // padding-bottom: 12px;
    // padding-top: 12px;
    padding-bottom: 10px;
    padding-top: 10px;
    text-decoration: none;
    font-family: $font-family;
}

.genesis-nav-menu a:focus,
.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item>a,
.genesis-nav-menu .sub-menu .current-menu-item>a:focus,
.genesis-nav-menu .sub-menu .current-menu-item>a:hover {
    color: $white-color;
    text-decoration: none;
    background-color: $base-color;
    border-radius: 6px;
}

.genesis-nav-menu .sub-menu,
.genesis-nav-menu .sub-menu a {
    width: 100%;
}

.genesis-nav-menu .sub-menu {
    clear: both;
    // display: none;
    // left: -9999px;
    margin: 0;
    opacity: 1;
    padding-left: 15px;
    position: static;
    z-index: 99;
}

.genesis-nav-menu .sub-menu a {
    background-color: #fff;
    // font-size: 17px;
    font-size: 15px;
    position: relative;
    word-wrap: break-word;
    color: #be0712;
    &::after{
        content: "\f061";
        color: #be0712;
        font-family: Font Awesome\ 5 Free;
        font-style: normal;
        font-weight: 900;
        font-size: 16px;
        position: absolute;
        right: 40px;
        opacity: 0;
        // margin-top: 2px;
        transition: all .6s ease;
    }
}
.genesis-nav-menu  {
    .sub-menu {
        li {
            background-color: #fff;
            overflow: hidden;
            display: block;
        }
        > li:first-child { 
            // border-top-left-radius: 6px;
            // border-top-right-radius: 6px;
        }
        > li:last-child {
            // border-bottom-right-radius: 6px;
            // border-bottom-left-radius: 6px;
        }
    }
}

.genesis-nav-menu .sub-menu a:hover {
    &::after{
        right: 20px;
        opacity: 1;
    }
}
.genesis-nav-menu .sub-menu .current-menu-item a:hover {
    color: $link-color;
    // &::after{
    //     color: $white-color;
    // }
}

.genesis-nav-menu .menu-item:focus,
.genesis-nav-menu .menu-item:hover {
    position: relative;
}

.genesis-nav-menu .menu-item>.sub-menu {
    border: none;
    background-color: $white-color;
    min-width: 170px;
    visibility: hidden;
    opacity: 0;
    transform: translateY(30%) scale(0.2) !important;
    display: block !important;
    transition: all 0.15s ease;
    padding: 5px;
    margin-top: 8px;
}
.genesis-nav-menu .menu-item:hover>.sub-menu {
    display: block;
    left: auto;
    opacity: 0;
    visibility: visible;
    transform: scale(1)!important;
    transition: all 0.25s ease;

}


/* Responsive Menu
--------------------------------------------- */

.menu .menu-item:focus {
    position: static;
}

.menu .menu-item>a:focus+ul.sub-menu,
.menu .menu-item.sfHover>ul.sub-menu {
    left: auto;
    opacity: 1;
}

.genesis-responsive-menu {
    display: none;
    position: relative;
}

.genesis-responsive-menu .genesis-nav-menu .menu-item:hover>.sub-menu {
    // display: none;
}

.menu-toggle,
.sub-menu-toggle {
    background-color: transparent;
    border-width: 0;
    color: $link-color;
    display: block;
    margin: 0 auto;
    overflow: hidden;
    text-align: center;
    visibility: visible;
}

.menu-toggle:focus,
.menu-toggle:hover,
.sub-menu-toggle:focus,
.sub-menu-toggle:hover {
    background-color: transparent;
    border-width: 0;
    color: $link-color;
}

.menu-toggle {
    float: right;
    line-height: 20px;
    margin-bottom: 10px;
    margin-top: 10px;
    padding: 15px 0;
    position: relative;
    z-index: 1000;
}

.menu-toggle.activated::before {
    content: "\f335";
}

.site-header .menu-toggle::before {
    float: left;
    margin-right: 5px;
    position: relative;
    text-rendering: auto;
    top: 1px;
}

.sub-menu-toggle {
    float: right;
    padding: 9px 10px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
}

.sub-menu .sub-menu-toggle {
    padding: 12px 10px;
}

.sub-menu-toggle::before {
    display: inline-block;
    text-rendering: auto;
    transform: rotate(0);
    transition: transform 0.25s ease-in-out;
}

.sub-menu-toggle.activated::before {
    transform: rotate(180deg);
}


/* Header Menu
--------------------------------------------- */

.nav-primary {
    clear: left;
    // padding-bottom: 15px;
    padding-top: 15px;
    padding-bottom: 2px;
    width: 100%;
}

/* Footer Menu
--------------------------------------------- */

.nav-secondary {
    margin-top: 10px;
}

.nav-secondary .genesis-nav-menu {
    line-height: 1.5;
}

.nav-secondary .menu-item {
    display: inline-block;
}

.nav-secondary a {
    margin-left: 10px;
    margin-right: 10px;
    padding: 0;
}

@media (min-width: 992px){
    #sfm-sidebar{
        display: none !important;
    }
}
@media screen and(max-width: 992px){
    html.sfm-mob-nav{
        margin-top: 0px !important;
    }
    .sfm-mob-nav.sfm-pos-right{
        margin-top: 32px;
        .admin-bar{
            #sfm-mob-navbar{
                top: 44px;
            }
        }
        #sfm-sidebar{
            .sfm-sidebar-close::after, .sfm-sidebar-close::before{
                background: $link-color;
            }
            .sfm-social{
                //top: -408px;
               // left: -22px;
                position: relative;
                display: block;
                margin-top: 30px;
                margin-bottom: 10px;
                li:hover{
                    a::after{
                        background: transparent;
                    }
                    a::before{
                        text-shadow: none;
                    }
                }
                li{
                    a{
                        color: $link-color;
                        font-size: 28px;
                        &::after{
                            background: transparent;
                        }
                    }
                }
            }
            .sfm-nav{
                min-height: 50vh;
                height: auto;
                max-height: none;
                margin-top: 0px;
                position: relative;
                display: block;
                padding-top: 20px;
                width: 225px;
                .sfm-va-middle{
                    height: auto;
                    width: 225px;
                    .sfm-submenu-visible{
                        >a{
                            background: $base-color;
                            color: $white-color;
                            i{
                                border-color: $white-color;
                            }
                        }
                    }
                }
            }
            .sfm-logo{
                position: relative;
                display: block;
                margin-top: 15px;
                margin-bottom: 30px;
            }
            &.sfm-hl-line .sfm-menu li:hover>a,
            &.sfm-hl-line .sfm-menu li:hover>a:focus{
                background: $base-color;
                color: $white-color;
                i{
                    border-color: $white-color;
                }
            }
        }
        #sfm-mob-navbar{
            text-align: left;
            padding-left: 12px;
            background-color: transparent;
            top: 0;
            transition: all 0s linear;
            .sfm-navicon{
                height: 2px !important;
                &::before{
                    height: 2px !important;
                }
                &::after{
                    height: 2px !important;
                }
            }
            &.transparent-menu-tablet{
                background-color: $black-color;
                transition: all 0s linear;
                img{
                    filter: brightness(0) invert(1);
                }
                .sfm-navicon, .sfm-navicon::after, .sfm-navicon::before{
                    background-color: $white-color;
                }
            }
            
        }
        .page-template-template-recetas, .single-receta{
            #sfm-mob-navbar{
                background-color: $black-color;
            }
        }
        .page-template-template-blog, .page-template-template-main-videos,
        .page-template-template-recetas, .single-receta {
            #sfm-mob-navbar{
                img{
                    filter: brightness(0) invert(1);
                }
                .sfm-navicon, .sfm-navicon::after, .sfm-navicon::before{
                    background-color: $white-color;
                }
            }
        }
    }
}

@media screen and (max-width: 782px){
    .sfm-mobile.sfm-mob-nav{
        margin-top: 46px;
    }
}
@media (max-width: 768px){
   
    .sfm-mob-nav.sfm-pos-right #sfm-sidebar .sfm-nav{
        height: 70%;
    }

}
@media  (max-width: 736px){
   
    .sfm-mob-nav.sfm-pos-right #sfm-sidebar .sfm-nav{
        height: auto;
    }

}
@media screen and (max-width: 725px){
    
    .sfm-pos-right{
        #sfm-sidebar{
            .sfm-compact{
                .sfm-logo{
                    margin-top: 0;
                }
            }
            .sfm-logo{
                top: 0px;
                text-align: center;
                //margin-top: -200px;
                img{
                    max-width: 100px;
                }
            }
            .sfm-nav{
                top: 15px;
                
                .sfm-menu{
                    li{
                        a{
                            padding: 20px 0;
                            .sfm-sm-indicator{
                                transform: rotate(180deg);
                                right: 10px;
                            }
                            span{
                                font-size: 18px;
                                font-weight: 700;
                            }
                        }
                    }
                }
            }
            
        }
    }
    #sfm-sidebar{
        overflow-y: scroll;
        background-color: white;
        &::-webkit-scrollbar {
            display: none;
        }
       
    }
    @-moz-document url-prefix() { /* Disable scrollbar Firefox */
        #sfm-sidebar{
          scrollbar-width: none;
        }
    }

    #sfm-sidebar .sfm-scroll-wrapper{
        height: auto !important;
        min-height: 100vh;
    }
    #sfm-sidebar .sfm-scroll-wrapper .sfm-scroll{
        min-height: calc(100vh - 50px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
   
    #sfm-sidebar .sfm-nav{
       flex-grow: 1;
    }
}
@media screen and (max-width: 600px){
    .page-template-template-recetas, .single-receta{
        #sfm-mob-navbar {
            position: absolute;
            &.transparent-menu-mobile{
                position: fixed;
            }
        }
    }
}