// Hero
.home {
  .site-header {
    background: transparent;
    position: fixed;
    top: 32px;
    &.transparent-menu{
      background-color: $black-color;
      .title-area{
        .main--logo{
          filter: brightness(0) invert(1);
        }
      }
      .nav-primary{
        .menu-item-has-children{
          a{
            span::after{
              color: $white-color;
            }
          }
        }
        a{
          color: $white-color;
        }
        .sub-menu{
          a{
            color: $link-color;
          }
        }
      }
    }
  }

  .title-area {
    .main--logo {
      display: block;
    }

    .second--logo {
      display: none;
    }
  }

  .genesis-nav-menu a {
    color: $base-color;
    display: block;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 700;
    outline-offset: -1px;
    // padding-bottom: 12px;
    // padding-top: 12px;
    padding-bottom: 10px;
    padding-top: 10px;
    text-decoration: none;
    font-family: $font-family;
    
  }

  .genesis-nav-menu a:focus,
  .genesis-nav-menu a:hover,
  .genesis-nav-menu .current-menu-item > a {
    color: #fff;
    text-decoration: none;
    background-color: $base-color;
    border-radius: 6px;
  }

  .genesis-nav-menu .sub-menu,
  .genesis-nav-menu .sub-menu a {
    // width: 100%;
    color: #be0712;
    // transform: scale(0.3);
  }

  .genesis-nav-menu .sub-menu a:hover {
    background-color: rgba(242, 242, 242,0.8);
  }
}

.section--hero {
  position: relative;

  .hero--items {
    z-index: 50;

    &__item {
        min-height: 890px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        display: flex;
        align-items: center;
        transition: transform 0.5s ease;
      .container {
        max-width: 740px;
      }

      .item__content {
        img {
          display: inline-block;
        }

        h2,
        p {
          color: $white-color;
          text-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);
        }

        h2 {
          margin-top: 1rem;
          margin-bottom: 0.5rem;
          @include font-responsive(68, 28);
        }

        p {
          font-size: 20px;
          max-width: 622px;
          margin: auto;
        }

        a {
          margin-top: 1rem;
          &:focus {
            outline: none;
          }
        }
      }
      @media screen and ( max-width: $tablet){
        min-height: 700px;
        .container{
            position: relative;
            //  bottom: 35px;
            bottom: -100px;
        }
      }
      @media screen and ( max-width: $small-tablet){
        min-height: 500px;
        .container{
            position: relative;
            bottom: 0px;
        }
      }
    }
    .item-video-bg{
      video{
        transition: transform 0.5s ease;
      }
    }

    .slick-slide {
      outline: none;
    }
  }
  .slick-dotted.slick-slider{
    margin-bottom: 0;
    }

  .section--download-hero {
    background: transparent;
    position: absolute;
    z-index: 100;
    top: 624px;
    left: 50%;
    transform: translate(-50%);
    margin-top: 25px;
    width: 100%;
    .section--download__content{
      min-height: 219px;
    }
    ._form-content {
      ._form-title,
      ._html-code {
        color: $white-color;
        text-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);
      }

      ._field-wrapper {
        input {
          background-color: #e4e4e4;
        }
      }
    }

    ._form-thank-you {
      color: $white-color;
      text-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);
    }
  }

  .hero--arrow {
    position: absolute;
    z-index: 110;
    right: 10%;
    bottom: 40px;
    animation: bottom 0.6s infinite alternate;

    a {
      min-height: 80px;
      @include flexbox;
      @include align-items(flex-end);
    }

    img {
      width: 30px;
      height: 35px;
    }
  }

  @include keyframes("bottom") {
    0% {
      bottom: 0;
    }

    100% {
      bottom: 40px;
    }
  }

  @media screen and(max-width: $tablet) {
    background-position: 30%;

    .hero--items {
    //   top: 430px;
    }

    .section--download-hero { 
      margin-top: 0px;
      position: relative;
      top: 0px;
      background-color: #F5F5F5;
      margin-bottom: 0;
      padding-bottom: 0;
      ._form_element{
        ._form-title{
          color: $link-color;
          font-size: 20px !important;
          text-shadow: 0 0 7px rgba(0,0,0,.3);
        }
        ._html-code{
          p{
            color: $link-color;
            font-size: 17px !important;
            font-weight: 600;
            text-shadow: 0 0 7px rgba(0,0,0,.3);
          }
        }
        @media (max-width: $tablet){
          ._form-title, ._html-code p{
            text-shadow: none;
          }
        }
      }
    }

    .hero--arrow {
      display: none;
    }
    .hero--items__item.slide-bg-desktop{
        display: none;
    }
    .hero--items__item.slide-bg-mobile{
        display: flex;
    } 
  }
  @media screen and (min-width: $medium-phone){
    .hero--items__item.slide-bg-desktop{
        display: flex;
    }
    .hero--items__item.slide-bg-mobile{
        display: none;
    }
    
  }

  // Dots
  @extend .custom--dots;
}

// Press
.section--press {
  .container{
    padding: 0;
  }
  min-height: 195px;
  background-color: $black-color;

  p {
    color: $white-color;
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
    padding-top: 2rem;
  }

  &__brands {
    margin: auto;
    bottom: 1rem;

    .brands__item {
      min-height: 80px;
      @include inline-flexcen(center, center);
      margin: 0 15px 0 30px;

      img {
        max-width: 100%;
      }
    }
    @media (max-width: 576px) {
      .brands__item {
        img{
          max-height: 80px;
        }
      }
    }
  }

  .slick-slide {
    outline: none;
  }
}

.section--download__resp{
  @media(max-width: 667px){
    #_form_3_submit{
      font-size: 15px!important;
    }
  }
}

// About
.section--about {
  background-color: $gray-color;
  padding: 3rem 0;
  position: relative;
  &__resp{
    @media(max-width:$medium-desktop){
      h3{
        font-size: 28px!important;
      }
    }
  }
  &__content {
    h3 {
      font-weight: 900;
      @include font-responsive(31, 20);
    }

    .image--eating-behind {
      display: none;
    }

    p {
      font-weight: 700;
      font-size: 18.84px;
      line-height: 1.45;
    }
  }

  &__images {
    .image--fruit-copy {
      display: none;
    }

    .image--vegetal-copy {
      display: none;
    }
  }

  .image {
    position: absolute;
    transition: all 0.1s linear;

    &.image--fruit {
      top: -18px;
      left: 19%;
      z-index: 10;
      width: 312px;
      
        @include responsive(width, 312, 220, 1700, 1100);
    }

    &.image--eating {
      top: 250px;
    //   top: 25%;
      left: 0;
      z-index: 30;
        @include responsive(width, 402, 302, 1700, 1100);
      
    }    

    &.image--vegetal {
    //   bottom: -20px;
      top: 456px;
      left: 18%;
      z-index: 20;
      @include responsive(width, 392, 292, 1700, 1100);
    }
  }

  @media screen and (max-width: $medium-desktop) {
    .container{
        max-width: 800px;
    }
    &__content {
      p {
        text-align: center;
        padding: 0 8px;
      }

      .image--eating-behind {
        display: block;
      }
    }

    .image--fruit {
      display: none;
    }

    .image--vegetal {
      display: none;
    }

    .image--eating {
      display: none;
    }

    &__images {
      .image--fruit-copy {
        display: block;
      }

      .image--vegetal-copy {
        display: block;
      }
    }
  }
  @media screen and(max-width: $phone){
      &__content{
          p{
              text-align: left;
          }
      }
      .image--eating-behind,
      .image--fruit-copy,
      .image--vegetal-copy{
          img{
              width: 100%;
          }
      }
  }
}

// Last Recipe
.section--posts {
  margin-top: 5rem;

  h4 {
    text-align: center;
    color: $black-color;
    margin-bottom: 2rem;
  }

  &__item {
    padding: 1rem 0.8rem;
    border: 1px solid transparent;
    background-color: $gray2-color;
    border-radius: 8px;
    transition: all 0.5s ease-in;
    height: 100%;
    .row{
      height: 100%;
    }

    .item__image {
      max-height: 231px;

      img {
        border-radius: 4px;
        min-height: 100%;
        height: 187px;
        width: 100%;
        object-fit: cover;
      }
    }
    .item--content {
        padding-right: 1.5rem;

        h6 {
            color: $link-color;
            margin-bottom: 0.5rem;
            margin-top: 0.5rem;
        }

        p {
            font-size: 16px;
            font-weight: 700;
            line-height: 1.294;
            max-width: 264px;

            @media screen and(max-width: $medium-phone) {
                margin: auto;
            }
        }

        a {
          margin-right: 3px;
          margin-top: 8px;
        }

        .item--content__title {
          display: block;
        }
        @media only screen and (max-width: $medium-phone) {
          a {
              margin-right: 2px;
              margin-left: 2px;
              margin-top: 18px;
          }
        }
        @media screen and(max-width: $medium-phone) {
          margin: auto;
          padding-right: 0;
        }
    }
    .item--content-resp{
      @media screen and(max-width: $medium-phone) {
        p{
          text-align: justify;
        }
      }
    }
    &:hover {
      border: 1px solid $link-color;
      box-shadow: 0px 3px 24px 0px rgba(0, 0, 0, 0.23);
      background-color: $white-color;
    }
  }

  &__link {
    margin-top: 1rem;

    a {
      border: 2px solid $link-color;
      color: $link-color;
    }
  }
  @media screen and ( max-width: 1200px){
      &__item{
        max-width: 800px;
        margin: auto;
          .col-sm-6{
              padding: 0 30px;
          }
      }
  }
  @media screen and(max-width: $medium-phone){
    &__item{
        max-width: 400px;
        .item__image{
            img{
                width: 100%;
            }
        }
        .item--content{
            p{
                max-width: 100%;
            }
        }
    }
  }
}


// eBook
.section--ebook {
  margin-top: 4rem;
  padding-bottom: 2rem;
  background-image: linear-gradient(
    to right,
    $black-color 0,
    $black-color 34.5%,
    $link-color 34.5%,
    $link-color 100%
  );
  position: relative;

  .col-xl-5 {
    min-height: 800px;
  }

  &__vertical {
    position: absolute;
    top: 50%;
    left: 34%;
    transform: translate(-75%, -50%);

    img {
      min-width: 574px;
      // width: 100%;
    }

    p {
      position: absolute;
      transform: rotate(-90deg);
      top: 31%;
      left: -78%;
      min-width: 875px;
      font-size: 89.95px;
      color: #151515;
      text-transform: uppercase;
      font-weight: 600;
    }
  }

  &__content {
    min-height: 875px;
    margin-left: 1.5rem;
    @include flexcen(center, flex-start);
    flex-direction: column;
    color: $white-color;
    font-weight: 600;

    h5 {
      font-weight: 700;
    }

    p {
      font-size: 17px;
      line-height: 1.706;
      margin-bottom: 0;
    }

    .ebook__links {
      margin: 2rem 0;
      @include flexbox;
      @include align-items(baseline);
