        //     @media only screen and (max-width: $medium-phone) {
        //         flex-wrap: wrap;
        //         text-align: center;
        //         a {
        //             width: 100%;
        //             margin-bottom: 25px;
        //         }
        //     }
        // }
    &__vertical {
      p {
        display: none;
      }
    }
  }

  @media screen and(max-width: $medium-desktop) {
    background-image: linear-gradient(
      to bottom,
      $black-color 0,
      $black-color 34.5%,
      $link-color 34.5%,
      $link-color 100%
    );

    &__vertical {
      transform: none;
      top: 5%;
      left: 50% !important;
