      to bottom,
      $black-color 0,
      $black-color 25%,
      $link-color 25%,
      $link-color 100%
    );

    .col-xl-5 {
      @include responsive(min-height, 800, 530);
    }

    &__vertical {
      img {
        @include responsive(min-width, 574, 350);
