// Juices
.section--juices {
  min-height: 509px;
  background-color: $gray-color;

  h3 {
    text-align: center;
    padding: 2.5rem 0;
    margin-bottom: 0;
  }

  &__slider {
    .juices--item {
      min-height: 245px;
      max-width: 360px;
      margin: 0 1rem;
      position: relative;
      transition: all 0.5s ease-in;
      @include flexcen(center, center);
      flex-direction: column;
      &::before{
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.2);
      }

      &__img {
        // display: none;
        height: 0;
        opacity: 0;
        position: relative;
      }

      &__imggreen {
        width: 60px;
        height: 60px;
        position: relative;
      }

      p {
        font-weight: 700;
        font-size: 18.76px;
        color: $white-color;
        text-align: center;
        margin-top: 1rem;
        margin-bottom: 1rem;
        line-height: 1.373;
        transition: all 0s linear;
        text-transform: capitalize;
        position: relative;
        // text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5);
      }

      .btn--red {
        min-height: 0;
        max-height: 0;
        opacity: 0;
        transform: scale(0.2);
        transition: all 0.3s ease;
        position: relative;
        // display: none;
      }

      &:hover {
        &::before {
          background: rgba(255,255,255,0.1);
        }

        @include animation-fade($fade);

        .juices--item__imggreen {
          height: 0;
          opacity: 0;
        }

        .juices--item__img {
          height: 60px;
          width: 60px;
          opacity: 1;
          // animation: $fade 0.8s ease;
        }

        p {
          color: $link-color;
          font-weight: 900;
        }

        .btn--red  {
          min-height: 46px;
          max-height: 46px;
          opacity: 1;
          transform: scale(1);
          @include inline-flex;
        }
      }

      @media screen and (max-width: $small-tablet) {
        @include animation-fade($fade);

        .juices--item__imggreen {
          height: 0;
          opacity: 0;
        }

        .juices--item__img {
          height: 60px;
          width: 60px;
          opacity: 1;
          // animation: $fade 0.8s ease;
        }

        p {
          color: $link-color;
          font-weight: 900;
        }

        .btn--red  {
          min-height: 46px;
          max-height: 46px;
          opacity: 1;
          transform: scale(1);
          @include inline-flex;
        }
        
      }
      @media screen and (max-width: $phone) {
        &:hover {
          &::after {
            background: linear-gradient(rgba(#8b8e92, 0.7), rgba(#0b0b0c, 0.7)),
              url("../../images/juices-img.png");
            animation: $fade 0s ease;
          }
        }

        .juices--item__imggreen {
          height: 0;
          opacity: 0;
        }

        .juices--item__img {
          height: 60px;
          width: 60px;
          opacity: 1;
          // animation: $fade 0.8s ease;
        }

        .btn--red {
          height: 46px;
          opacity: 1;
          border: 2px solid $link-color;
          @include inline-flex;
        }

        p {
          color: $link-color;
          font-weight: 900;
        }
      }
    }
  }

  .btn--transparent-red {
    margin-top: 2.5rem;
    z-index: 10;
  }

  .slick-slide {
    outline: none;
  }
  .slick-dots{
    bottom: -40px;
    button::before{
        content: "";
        border: 2px solid $link-color;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        opacity: 1!important;
    }
    .slick-active{
        button::before{
            background-color: $link-color;
        }
    }
}
}

// Form
.section--form {
  &__content {
    min-height: 297px;
    @include flexcen(center, center);
    flex-direction: column;

    p {
      max-width: 620px;
      text-align: center;
      font-size: 20px;
      font-weight: 700;
      margin-bottom: 0;
    }

    ._form-content {
      position: relative;

      ._button-wrapper {
        position: absolute;
        right: 4% !important;
        top: 20%;
        width: 97px !important;
      }

      ._field-wrapper {
        input {
          &::placeholder {
            font-size: 18px;
            font-weight: 700;
            color: $black-color;
            font-family: $font-family;
            text-transform: uppercase;
            padding-left: 10px !important;
          }
        }
      }
    }

    @media screen and (max-width: $tablet) {
      ._field-wrapper {
        input {
          max-width: 320px !important;

          &::placeholder {
            font-size: 14px !important;
          }
        }
      }

      ._button-wrapper {
        ._submit {
          font-size: 15px !important;
        }
      }
    }

    ._form-branding {
      display: none;
    }
  }
}

// Podcast
.section--podcast {
  //Para tarea: "Esconder el bloque de PODCAST y Testimonios con CSS temporalmente del homepage."
  display: none!important;
  //
  min-height: 715px;
  padding-bottom: 30px;
  background-image: linear-gradient(
    to right,
    $link-color 0,
    $link-color 34.5%,
    $black-color 34.5%,
    $black-color 100%
  );
  position: relative;
  @include flexcen(center, center);

  &__content {
    margin-left: 1.5rem;
    color: $white-color;
    font-weight: 700;
    position: relative;
    z-index: 100;

    .podcast__title {
      @include min-w-h(291, 72);
      background-color: $link-color;
      @include inline-flexcen(center, center);
      font-size: 61.24px;
      text-transform: uppercase;
      max-height: 72px;
    } 

    p {
      font-size: 20px;
      line-height: 1.45;
      margin: 1rem 0;
      font-weight: 600;
    }

    .podcast--subscribe {
      min-height: 18px;
      min-width: 100%;
      margin: 2rem 0;
      position: relative;

      span {
        display: block;
        position: absolute;
        top: 0;
        left: 48%;
        transform: translate(-50%, -50%);
        text-transform: uppercase;
        font-size: 26px;
        min-width: 60%;
        text-align: center;
      }

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 4px;
        width: 22%;
        background-color: $link-color;
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 5%;
        height: 4px;
        width: 22%;
        background-color: $link-color;
      }
    }

    .podcast--item {
      padding: 0;
      position: relative;
      @include flexcen(flex-start, center);

      a {
        margin: 0.3rem 0;
        img{
          max-width: 40px;
          max-height: 40px;
          border-radius: 20%;
        }
        &::after{
          content: "";
          position: absolute;
          width: 200px;
          height: 100%;
          left: 0;
          top: 0;
        }
      }

      p {
        margin: 0;
        margin-left: 1rem;
        font-size: 19px;
      }
    }

    .podcast__link {
      margin-top: 1.5rem;
    }
  }

  &__phone {
    position: absolute;
    bottom: 0;
    left: calc(41% - 380px);
    // left: 31%;
    // left: responsive(100, 48, 1500, 1200);
    // transform: translate(-60%, 0);
    min-width: 384px;
    z-index: 30;
  }

  &__man {
    position: absolute;
    right: 0;
    top: -2px;
  }

  &__wave {
    position: absolute;
    left: 0;
    top: 20%;
    @include responsive(width, 694, 100, 1750, 420);
    z-index: 10;
  }

  &__link {
    position: absolute;
    top: calc(50% - 10px);
    left: -10px;
    transform: rotate(-90deg);
    z-index: 20;

    a {
      font-size: 20px;
      text-transform: uppercase;
      font-weight: 600;
      color: $white-color;
    }
  }
  .resp-esp {
    @media screen and(max-width: $medium-desktop) {
      min-height: 300px!important;
    }
    @media screen and(max-width: $medium-phone) {
      min-height: 350px!important;
    }
    @media screen and(max-width: $phone){
      min-height: 200px!important;
    }
  }
  @media screen and(max-width: $medium-desktop) {
    background-image: linear-gradient(
      to right,
      $black-color 0,
      $black-color 50%,
      $black-color 50%,
      $black-color 100%
    );
    margin-top: 250px;

    &__content {
      margin-left: 0;
      margin-top: 100px;
      .col-sm-6 {
        max-width: 400px;
        .podcast--item{
            margin-left: 80px;
        }
      }
      p {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
      }

    // @media screen and(max-width: $medium-desktop) {
    //     background-image: linear-gradient(to right,
    //             $black-color 0,
    //             $black-color 50%,
    //             $black-color 50%,
    //             $black-color 100%);
    //     margin-top: 300px;
    //     padding-bottom: 40px;
    //   .podcast--item {
    //     @include align-items(center);
    //     margin-left: responsive(150, 10);
    //   }

      .podcast--subscribe {
        span {
          left: 50%;
          @include font-responsive(26, 18);
        }
        &::before{
            right: 10%;
        }
        &::after{
            left: 10%;
        }
      }
    }

    &__phone {
      bottom: 65%;
      left: 50%;
      transform: translate(-50%, 0);
      @include responsive(min-width, 384, 250);
    }

    &__wave {
      display: none;
    }

    &__link {
      display: none;
    }

    &__man {
      min-width: 720px;
      top: -1%;

      img {
        width: 100%;
      }
    }
  }

  @media screen and(max-width: $tablet) {
    &__content {
      h1 {
        @include font-responsive(30, 20);
      }

      .podcast__title {
        @include font-responsive(61.24, 45);
      }
    }
  }

  @media screen and(max-width: $phone) {
    padding: 2rem 0;
    margin-top: 200px;

    .col-xl-5 {
      // min-height: 350px;
      //@include responsive(min-height, 350, 200);
    }
    //$medium-phone: 576px
    @media only screen and (max-width: $medium-phone) {
        &__content {
            .podcast--item {
                margin-left: responsive(175, 90, 576, 360);
            }
        }
    }

    &__content {
      h1 {
        margin-top: 25px;
      }

      .podcast--subscribe {
        margin-top: 3rem;

        &::after {
            width: 18%;
            left: 6%;
        }

        &::before {
            width: 18%;
            right: 6%;
        }
      }

      .podcast--item {
        p {
          margin-left: 10px;
          @include font-responsive(19, 16);
        }
      }
    }

    &__phone {
      bottom: 75%;
    }
  }
}

// Testimonial
.section--testimonial {
  //Para tarea: "Esconder el bloque de PODCAST y Testimonios con CSS temporalmente del homepage."
  display: none!important;
  //
  min-height: 600px;

  &__slider {
    min-height: 510px;
    padding: 4rem 0;
    @include flexbox;
    @include align-items(flex-start);

    .testimonial--item {
      padding: 2rem;
      margin: 4rem 2.5rem 0 2.5rem;
      min-height: 290px;
      position: relative;
      border: 1px solid #696969;
      transition: all 0.5s ease-in;

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 10%;
        width: 80%;
        height: 9px;
        background-color: transparent;
        transition: all 0.5s ease-in;
      }

      &:hover {
        border: 1px solid $link-color;

        &::after {
          background-color: $link-color;
        }
      }

      p:nth-child(1) {
        margin-top: 3rem;
        font-size: 17px;
        font-style: italic;
        font-weight: 600;
        line-height: 1.294;
      }

      p:nth-child(2) {
        font-size: 18px;
        font-weight: 700;
        color: $link-color;
        text-transform: uppercase;
        margin-bottom: 0;
      }

      span {
        font-size: 15px;
        font-weight: 300;
        color: rgb(32, 29, 30);
      }

      &__img {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
      }
    }
  }

  &__slider .testimonial--item:nth-child(odd) {
    margin-top: 10rem;

    @media screen and(max-width: $small-tablet) {
      margin-top: 4rem;
    }
  }

  @extend .custom--dots-test;

  .slick-slide {
    outline: none;
  }
}

// Social
.section--social {
  background-color: #b9b5c0;
  @include bg-image(
    "../images/social-person.png",
    left center,
    no-repeat,
    content
  );
  @media (max-width: $small-tablet) {
    background-color: inherit;
    background-image: inherit;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.2), rgba(0,0,0,0.5)),url('../../images/social-person.png');
    background-position: 0;
    background-repeat: no-repeat;
    background-size: content;
  }
  &__content-resp{
      @media(max-width:$tablet){
        h5,h6{
          color:$white-color!important;
        }
      }
  }
  &__content {
    min-height: 675px;
    position: relative;
    @include flexcen(center, flex-start);
    flex-direction: column;
    padding-left: 47px;
    h5 {
      margin-bottom: 0;
    }

    h6 {
      font-size: 35px;
    }

    .social {
      max-width: 575px;
      width: 100%;
      min-height: 58px;
      border-radius: 5px;
      margin-bottom: 1rem;
      font-weight: 700;
      @include flexcen(flex-start, center);

      i {
        font-size: 30px;
        color: $white-color;
        margin-left: 2rem;
      }

      p {
        margin-bottom: 0;
        font-size: 24px;
        text-transform: uppercase;
        color: $white-color;
        margin-left: 1rem;
      }

      span {
        background-color: $white-color;
        font-size: 27px;
        margin-left: auto;
        margin-right: 0.5rem;
        border-radius: 5px;
        padding: 0 0.5rem;
        min-width: 79px;
        text-align: center;
      }

      &.social--facebook {
        background-color: #1c7bf7;
      }

      &.social--linkedin {
        background-color: #1178ab;
      }

      &.social--youtube {
        background-color: $white-color;

        p,
        i {
          color: #fc131c;
        }
      }

      &.social--instagram {
        position: relative;
        background-image: linear-gradient(to right, #ffe872, #d81486, #7247d1);

        .instagram__arrow {
          position: absolute;
          right: -6%;
          bottom: -100%;
        }
      }

      &:hover {
        //box-shadow: 0 0 10px 1px $link-color;
        background:  #ff9999;
        p,i{
          color: $white-color;
        }
      }

      @media screen and(max-width: $phone) {
        i {
          font-size: 40px;
        }

        p {
          font-size: 18px;
          text-align: center;
        }

        &.social--instagram {
          .instagram__arrow {
            right: 0%;
            bottom: -170%;
          }
        }
      }
    }

    @media screen and (max-width: $tablet) {
      @include align-items(center);
      padding-left: 0;
      h5,
      h6 {
        text-align: center;
      }
    }
  }
}

