.media--hero{
    .animation--background{
        background-image: none;
    }
    .podcast--hero__content{
        min-height: 428px;
        span{
            margin-bottom: 0;
            width: 345px;
            height: 81px;
            font-size: 55px;
            // @include font-responsive(55, 30);
            line-height: 1.5;
            display: none;
        }
    }
}

.media--about{
    // min-height: 682px;
    padding-top: 70px;
    .container{
        max-width: 1200px;
    }
    h2{
        color: $link-color;
        font-size: 30px;
        @media screen and (max-width: $small-phone){
            width: calc(100% - 30px);
            margin-left: auto;
            margin-right: auto;
        }
    }
    p{
        font-size: 18px;
        font-weight: 600;
    }
    @media screen and (max-width: $small-tablet){
        padding-top: 35px;
        h2{
            text-align: center;
        }
        p{
            text-align: justify;
        }
    }
}

.media--brands{
    min-height: 388px;
    background-color: $black-color;
    position: relative;
    padding-top: 234px;
    margin-top: 240px;
    &__video{
        max-height: 386px;
        height: 100%;
        max-width: 705px;
        width: 100%;
        padding: 0 15px;
        position: absolute;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        left: 50%;
        top: 0;
        transform: translate(-50%, -50%);
        video{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
        a{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
        @media screen and (max-width: $small-tablet){
            height: auto;
        }
    }
    h2{
        color: $white-color;
        font-size: 30px;
        font-weight: 700;
        text-align: center;
        margin-bottom: 35px;
    }
    &__slider{
        // margin-top: 35px;
        // min-height: 150px;
        height: 0;
        overflow: hidden;
        opacity: 0;
        // display: none;
        visibility: hidden;
        transition: all 0.9s ease;
        &.show-kits{
            height: 150px;
            opacity: 1;
            visibility: visible;
        }
        .brands--item{
            min-height: 147px;
            margin-top: 10px;
            .brands--move{
                margin: 0 15px;
                // width: 137;
                // position: relative;
                &__content{
                    position: relative;
                    animation: brands-animate1 5s ease-in-out infinite;
                    transition: all 0.3s ease-in-out;
                    img{
                        max-width: 130px;
                        height: 135px;
                        width: 100%;
                        object-fit: contain;
                        border-radius: 4px;
                    }                
                }
                
            } 
        }
        .slick-slide:nth-child(2n) .brands--move__content{
            animation: brands-animate2 5s  infinite;
        }
        .slick-slide:nth-child(3n+2) .brands--move__content{
            animation: brands-animate3 5s infinite;
        }
        .slick-slide:nth-child(6n+2) .brands--move__content{
            animation: brands-animate4 5s  infinite;
        }
        @keyframes brands-animate1{
            0%{ transform: translate(0px, 0px)}
            40%{transform: translate(5px, 8px)}
            80%{transform: translate(-5px, 4px)}
            100%{transform: translate(0px, 0px)}
        }
        @keyframes brands-animate2{
            0%{ transform: translate(0px, 0px)}
            40%{transform: translate(-5px, -8px)}
            80%{transform: translate(5px, -4px)}
            100%{transform: translate(0px, 0px)}
        }
        @keyframes brands-animate3{
            0%{ transform: translate(0px, 0px)}
            40%{transform: translate(-2px, -4px)}
            80%{transform: translate(-4px, -5px)}
            100%{transform: translate(0px, 0px)}
        }
        @keyframes brands-animate4{
            0%{ transform: translate(0px, 0px)}
            40%{transform: translate(3px, 5px)}
            80%{transform: translate(5px, 6px)}
            100%{transform: translate(0px, 0px)}
        }
    }

    @media screen and (max-width: $small-tablet){
        margin-top: 160px;
        padding-top: 160px;
        min-height: auto;
    }
}

