.single-video{
    #genesis-content{
        width: 100%;
    }
}
.hero-video{
    background-color: #be0712;
    padding-top: $margin-top;
    overflow: hidden;
    @media(max-width:991px){
        padding-top: 0;
        
    }
}
.page-hero{
    min-height: 430px;
    transition: transform 0.5s ease;
    @media(max-width:767px){
        min-height: 250px;
        background-position: left!important;
    }
}
.block-video-youtube {
    .video__suscribe{
        margin-bottom: 30px;
    }
    .btn-youtube{
        background-color: #f70100;
        color: #fff;
        padding: 13px;
        min-width: 252px;
        font-weight: 900;
        border-radius: 5px;
    }
    .video__image{
        position: relative;
        margin-top: -320px;
        margin-bottom: 45px;
        img{
            width: 800px;
            height: 450px;
            object-fit: cover;
            margin: auto;
            display: block;
            box-shadow: 0 0 12px 0 #666166;
            @media (max-width:991px){
                width: 75%;
            }
            @media (max-width:767px){
                height: 300px;
                width: 100%;
            }
        }
        .play{
            width: 90px;
            height: auto;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
            box-shadow: none;
        }
        @media(max-width:767px){
            margin-top: -100px;
        }
    }
}
#wpdevar_comment_1{
    margin-top: 50px;
}
.section--download {
    ._inline-style{
        display: block !important;
    }
}
.block-video-related{
    padding-top: 90px;
    h4{
        text-transform: initial;
        font-size: 25px;
        font-weight: 900;
        @media(max-width: $medium-phone){
            text-align: center;
        }
    }
    .video__image{
        position: relative;
        .play{
            
                width: 48px;
                height: auto;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translateY(-50%) translateX(-50%);
            
        }
    }
    .video__item{
        img{
            height: 215px;
            width: 100%;
            object-fit: cover;
        }
        a{
            cursor: pointer;
            p{
                font-size: 20px;
                color: #000;
                font-weight: 800;
                margin-top: 20px;
                @media(max-width: $medium-phone){
                    text-align: center;
                }
            }
        }
    }
    @media (max-width: $small-tablet ){
        padding-top: 50px;
    }
}
.video-content{
    &.custom-content{
        max-width: 1090px;
        margin-left: auto;
        margin-right: auto;
        h2{
            font-size: 35px;
            text-align: center;
            text-transform: lowercase;
            &::first-letter{
                text-transform: uppercase;
            }
            @media(max-width: 767px){
                font-size: 30px;
            }
        }
        p{  
            text-align: center;
            font-size: 17px;
            line-height: 30px;
            font-weight: 600;
            @media(max-width: $medium-phone){
                text-align: justify;
            }
        }
       
    }
}

.book {
    .book--vertical{
        img{
            @media (max-width: $small-tablet){
                height: auto !important;
            }
        }
    } 
}
