.section--social {
    &__content {
        &.social--instagram {
            .instagram__arrow {
                bottom: -120%;
            }
        }
    }
}

// Instagram
.section--instagram {
    margin-bottom: 0;
}

.pagination {
    font-size: 18px;
    width: 100%;
    font-weight: 700;
    text-align: center;
    a{
        color: #000;
        padding: 10px 20px;
    }
    .current{
        background-color: #be0712;
        color: #fff;
        display: inline-block;
        padding: 10px 20px;
        border-radius: 5px;
    }
}