.podcast--hero{
    margin-top: $margin-top;
    position: relative;
    overflow: hidden;
    .animation--background{
        background-position: center;
        background-image: url("../../images/hero-podcast.jpg");
    }

    .container{
        max-width: 1184px;
        position: relative;
        z-index: 1;
    }
    
    &__content{
        min-height: 445px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-bottom: 32px;
        span{
            width: 300px;
            height: 74px;
            background-color: $link-color;
            font-size: 58px;
            // @include font-responsive(58, 30);
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            color: $white-color;
            line-height: 1.3;
            margin-bottom: 30px;
        }
          
        .podcast--icons{
            ul{
                display: flex;
                a{
                    margin: 6px;
                    img{
                        max-width: 38px;
                        border-radius: 8px;
                        transition: all .25s linear;
                        &:hover{
                            display: inline-block;
                            transform: translateY(-5px); 
                        }
                    }
                }
            }
        }
    }
    @media screen and(max-width: $tablet){
        // background-position: 27% 50%;
        margin-top: 0;
        .animation--background{
            // background-position: 0 50%;
        }
        .podcast--hero__content{
            justify-content: flex-end;
            padding-bottom: 80px;
        }
        &__content{
            .podcast--icons{
                ul{
                    a{
                        margin: 4px;
                        img{
                            max-width: 34px;
                        }
                    }
                }
            }
        }
    } 
}
.search--podcast{
    ul{
        .sf-field-search{
            label{
                input::placeholder{
                    color: #b6b6b6;
                }
            }
        }
    }
    .sf-field-taxonomy-podcast-category:not(:first-of-type) {
        display: none;
        margin-top: 25px;
        select{
            outline: none;
        }
        @media screen and(max-width: $small-tablet){
            display: flex;
            align-items: center;
            justify-content: center;
            h4{
                display: none;
            }
            label{
                width: 100%;
            }
        }
    }
    @media screen and(max-width: $small-tablet){
        .sf-field-taxonomy-podcast-category:first-child{
            display: none;
        }
        .section--filter__content{
            // margin: 0;
        }
    }
}
.podcast--blog{
    margin-top: 38px;
    .container{
        padding-right: 0;
    }
    .blog--item{
        >a{
            p{
                text-transform: initial;
            }
        }
    }
    .podcast--item{
        height: 467px !important;
        max-width: 374px;
        background-color: #ebebeb;
        img{
            min-height: 100%;
        }
    }
    @media screen and (max-width: $medium-desktop){
        .container{
            padding-left: 0;
        }
    }
    @media screen and (max-width: $tablet){
        .container{
            padding: 0 30px;
        }
        .section--blog__slider{
            margin: 0;
        }
        .podcast--item{
            margin: auto;
            max-width: 480px;
        }
    }
}