.progress {
    &__container {
        left: 0;
        width: 100%;
        height: 15px;
        margin-bottom: 0px;
        position: fixed;
        bottom: 0px;
        overflow: hidden;
        content: "";
        display: block;
        table-layout: fixed;
        z-index: 99;
    }
    &__bar {
        width: 0%;
        float: left;
        height: 100%;
        max-width: 100%;
        background-color: $red-color;
        transition: width .6s ease;
    }
}
