}

.preloader {
	position: fixed;
	left: 0px;
	top: 0px;
	width: 100%;
	height: 100%;
	z-index: 9999;
	background: url('../../images/loader.gif') 50% 50% no-repeat rgb(255,255,255);
}
.preloader-media {
	position: fixed;
	left: 0px;
	top: 0px;
	width: 100%;
	height: 100%;
	z-index: 9999;
	background: url('../../images/loader.gif') 50% 50% no-repeat rgb(255,255,255);
}

