/* Home */
.item-video-bg{
    video{
        width: 100%;
        height: 100%;
    }
}
.section--juices__slider .juices--item{
    max-width: 100%;
    width: 360px;
}

.section--juices__slider .juices--item p{
    @media (max-width: 520px) {
        color: #fff !important;
    }
}
.section--juices__slider .juices--item .btn--red{
    @media (max-width: 520px) {
        min-width: 105px;
        min-height: 46px !important;
        transform: none !important;
    }
}

.section--ebook__content .ebook__links a:hover:after {
    width: calc(100% + 4px) !important;
}
.site-header{
    @media (max-width: 991px){
        display: none;
    }
}
.sfm-rollback {
    @media (min-width: 991px){
        display: none;
    }
}
.section--juices__slider .juices--item__imggreen{
    outline: none;
}
.search-no-results {
    .section--download {
        display: none;
    }
    .section--ebook {
        display: none;
    }
    .section--blog{
        margin-bottom: 1.5rem;
    }
}
.sf-field-post_type{
    ul{
        li{
            &:nth-child(1){
                order: 2;
            }
            &:nth-child(2){
                order: 3;
            }
            &:nth-child(3){
                order: 1;
            }
        }
    }
}
.book{
